.filter-block {
	&__row {
		display: flex;
		align-items: flex-start;
	}

	&__main {
		display: flex;
		align-items: center;
		width: 85%;

		@include above($desktop) {
			align-items: normal;
		}

		@include above($mid-desktop) {
			width: 80%;
		}

		@include above($big-desktop) {
			width: 58%;
		}
	}

	&__main-inner {
		width: 60%;
	}

	&__items {
		margin-bottom: -2rem;
		margin-left: -1rem;
		margin-right: -1rem;
	}

	&__item {
		margin-bottom: 2rem;
		padding-left: 1rem;
		padding-right: 1rem;
	}

	&__change {
		margin-left: auto;
	}

	&__clear {
		display: flex;
		align-items: center;

		height: 50px;
		margin-left: 2rem;

		transition: opacity #{$transition};
		pointer-events: none;

		opacity: 0;
	}

	&__clear.is-active {
		pointer-events: auto;

		opacity: 1;
	}

	&__clear .link {
		white-space: nowrap;
	}

	&__show-more {
		@include indent-level(padding-top, orange);
	}

	&__sort-adaptive {
		display: block;

		@include above($desktop) {
			display: none;
		}
	}

	.slide-right-enter-active,
	.slide-right-leave-active {
		transition: transform $transition;
	}

	.slide-right-enter {
		transform: translateX(-100%);
	}

	.slide-right-enter-to {
		transform: translateX(0%);
	}

	.slide-right-leave {
		transform: translateX(-100%);
	}

	.slide-right-leave-to {
		transform: translateX(-100%);
	}

	.fade-chips-enter-active,
	.fade-chips-leave-active {
		transition: transform $transition-anim, opacity $transition-anim;
	}

	.fade-chips-enter {
		transform: translateY(-2rem);

		opacity: 0;
	}

	.fade-chips-enter-to {
		transform: translateY(0rem);

		opacity: 1;
	}

	.fade-chips-leave {
		transform: translateY(-2rem);

		opacity: 0;
	}

	.fade-chips-leave-to {
		transform: translateY(-2rem);

		opacity: 0;
	}
}
