.accordion {
    &__body {
        transition: height $transition-anim, padding $transition-anim, margin $transition-anim;
        overflow: hidden;
    }
    &__body--desktop--open {
        @include above($desktop){
            height: auto !important;

        }
    }
    &__body--line-clamp {
        height: auto !important;
        
        display: -webkit-box;
        -webkit-line-clamp: 15;
        -webkit-box-orient: vertical;


    }
    &__body--line-clamp + &__heading{
        @include indent-level(margin-top, green);
    }
    &.is-open &__body--line-clamp{
        -webkit-line-clamp: unset;
    }
    &:not(.is-open) &__body{
        height: 0;
        margin: 0;
    }
    &__heading {
        cursor: pointer;

    }
    &.is-open &__heading {
        .link__icon {
            transform: rotate(180deg)
        }
    }
    &__text--is-open{
        display: none;
    }

    &__text--not-open{
        display: block;
    }

    &.is-open &__text--is-open{
        display: block;
    }
    &.is-open &__text--not-open{
        display: none;
    }
}