.table-block {
	margin-left: -2rem;
	margin-right: -2rem;

	@include above($desktop) {
		margin-left: 0;
		margin-right: 0;
	}

	&__inner {
		min-height: 14rem;
		padding: 4.5rem 2rem;

		background-color: color(blue-sky);
		background-size: auto 90%;
		background-repeat: no-repeat;
		background-position: center 2rem;

		@include center-flex;
	}

	&__buttons {
		margin: -.5rem -1rem;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
	}

	&__button {
		padding: .5rem 1rem;
		text-align: center;
	}
}
