@import './config';

.radio {
	display: flex;
	align-items: center;

	cursor: pointer;

	color: $radio-color;

	@include text-base-medium;

	&:hover &__icon {
		@include above($desktop) {
			@media (hover: hover) {
				box-shadow: $radio-hover-shadow;
			}
		}
	}

	&__icon {
		display: flex;
		flex-shrink: 0;

		width: 1.8rem;
		height: 1.8rem;
		margin-right: .7rem;

		transition: background #{$transition}, box-shadow #{$transition};

		border-radius: 100%;

		background: #ffffff;
		box-shadow: $radio-shadow;
	}

	input {
		@include hidden;

		&:checked {
			& ~ .radio__icon {
				box-shadow: $radio-checked-shadow;
			}
		}

		&.error {
			& ~ .radio__icon {
				box-shadow: $radio-error-shadow;
			}
		}
	}

	label.error {
		display: none !important;
	}

	&--btn {
		height: 100%;
		min-height: 6rem;
		padding: 2rem;

		transition: border-color #{$transition};

		border: 1px solid color(dark-10);
		background-color: color(white-main);

		@include above($tablet) {
			padding-right: 4rem;
			padding-left: 4rem;
		}

		&:hover {
			@media (hover: hover) {
				border-color: color(dark-main);
			}
		}
	}
}
