$delay: .7s;

.first-screen-slider {
	$delay: .7s;

	position: relative;

	@include above($desktop) {
		height: 100vh;
	}

	&::after {
		position: absolute;
		top: 0;
		left: 50%;

		display: none;

		width: 50%;
		height: 100%;

		content: '';

		background: color(blue-bg);

		@include above($desktop) {
			display: block;
		}
	}

	&__container {
		height: 100%;
		width: 100%;
		padding-bottom: 11.5rem;
		padding-top: 10.2rem;

		@include above($desktop) {
			padding-bottom: 0;
		}
	}

	&__slide {
		display: flex;

		height: 100%;

		transition: all $delay ease;
	}

	&__info {
		display: flex;
		flex-direction: column;
		justify-content: space-evenly;

		width: 100%;
		height: 100%;
		padding: 0 2rem;

		@include above($desktop) {
			width: 50%;
			padding: 0 3.8rem 0 6rem;
		}

		@include above($mid-desktop) {
			padding: 0 3.8rem 0 12.9rem;
		}

		@include above($large-desktop) {
			padding: 0 9.6rem 0 15rem;
		}

		@include above($big-desktop) {
			padding: 0 10.8rem 0 19.1rem;
		}

		&::before {
			position: absolute;
			bottom: 0;
			left: 2rem;

			display: none;

			top: -12rem;

			content: '';
			transition: transform $delay ease;
			transition-delay: $delay;
			transform: scaleY(0);
			transform-origin: top center;

			border-left: .2rem solid color(green-main);
			will-change: transform;

			@include above($desktop) {
				display: block;
			}

			@include above($mid-desktop) {
				left: 3.9rem;
			}

			@include above($large-desktop) {
				left: 4.9rem;
			}

			.swiper-container-initialized & {
				transform: scaleY(1);
			}
		}
	}

	&__img {
		display: none;

		width: 50%;
		height: 100%;

		transition: opacity $delay ease, transform $delay ease;
		transition-delay: $delay;
		transform: translateX(2.5rem);

		opacity: 0;

		@include above($desktop) {
			display: block;
		}

		.swiper-slide-active & {
			transform: translateX(0);

			opacity: 1;
		}

		img {
			display: block;

			width: auto;
			max-width: 100%;
			height: 100%;
			margin: 0 auto;
			object-fit: contain;
			object-position: bottom;
		}
	}

	&__text {
		max-width: 54rem;
		margin-bottom: 3rem;

		transition: opacity $delay ease, transform $delay ease;
		transition-delay: $delay;
		transform: translateY(-2.5rem);

		opacity: 0;

		@include above($desktop) {
			max-width: 44rem;
			margin-bottom: 0;
		}

		@include above($big-desktop) {
			max-width: 50rem;
			margin-bottom: 0;
		}

		.swiper-slide-active & {
			transform: translateY(0);

			opacity: 1;
		}
	}

	&__title-mark {
		@include above($desktop) {
			display: block;
		}
	}

	&__facts {
		transition: opacity $delay ease, transform $delay ease;
		transition-delay: $delay;
		transform: translateY(2.5rem);

		opacity: 0;

		.swiper-slide-active & {
			transform: translateY(0);

			opacity: 1;
		}
	}

	&__fact-number {
		font-size: 4.5rem;
		line-height: 1.4;
		font-weight: 700;

		color: color(dark-main);
	}

	&__count {
		position: absolute;
		z-index: 1;
		top: 50%;
		left: 50%;

		display: none;

		transform: translateY(-50%) translateX(-50%);

		@include above($desktop) {
			display: flex;

			transition: opacity $delay ease;
			transition-delay: $delay;

			opacity: 0;
		}

		.swiper-container-initialized & {
			opacity: 1;
		}

		.odometer * {
			transition-duration: 1.4s !important;
		}

		.odometer-inside {
			font-size: 0;
		}

		.odometer-digit {
			font-size: 10rem;
		}
	}

	&__count-value {
		font-weight: 800;
		font-size: 10rem;
		font-family: inherit !important;
		line-height: 1 !important;

		letter-spacing: -.04em;

		color: transparent;

		-webkit-text-stroke: .1rem color(green-main);
		width: 7.5rem;

		&:first-of-type {
			transform: translateX(8px);
		}

		&:last-of-type {
			transform: translateX(-4px);
		}

		div, span {
			width: 100%;
		}
	}

	&__arrows {
		position: absolute;
		z-index: 1;
		bottom: 3rem;
		left: calc(50% + 4rem);

		display: none;

		@include above($desktop) {
			display: block;
		}
	}

	&__dots {
		position: absolute;
		z-index: 1;
		bottom: 5rem;
		left: 50%;

		width: auto;

		transform: translateX(-50%);

		@include above($desktop) {
			display: none;
		}

		@include above($mid-desktop) {
			right: 4rem;
			bottom: 50%;
			left: auto;

			display: flex;
			flex-direction: column;

			transform: translateY(50%) translateX(0);
		}

		@include above($large-desktop) {
			right: 5rem;
		}

		.slider-dots__item {
			margin: .3rem;
		}
	}

	&__under-bottom {
		position: absolute;
		z-index: 1;
		left: 5rem;
		bottom: 3rem;

		display: none;

		cursor: pointer;

		@include above($desktop) {
			display: block;
		}

		@include above($mid-desktop) {
			left: 7rem;
		}

		@include above($large-desktop) {
			left: 9rem;
		}

		@include above($big-desktop) {
			left: 12rem;
		}

		&:hover {
			.link {
				@media (hover: hover) {
					transform: translateY(3rem);
				}
			}
		}

		.link {
			transform: translateY(0);
			transition: transform $transition;
		}
	}

}
