.trainings {
	.slider-block--custom {
		@include above($desktop) {
			margin-left: -6rem;
			margin-right: -5rem;
		}

		@include above($mid-desktop) {
			margin-left: 0;
			margin-right: 0;
		}
	}

	.slider-block__init--custom {
		@include indent-level(margin-bottom, blue, 'y', true);

		@include above($desktop) {
			padding-left: 5rem;
		}

		@include above($mid-desktop) {
			overflow: visible;
			flex-wrap: wrap;

			padding-left: 0;
		}
	}

	.slider-block__slide--custom {
		@include indent-level(margin-bottom, blue);
	}

	&--border {
		@include above($mid-desktop) {
			position: relative;
		}

		&::before {
			@include above($mid-desktop) {
				position: absolute;
				top: 0;
				left: 4rem;

				display: block;

				height: 70%;

				content: '';

				border-left: .2rem solid color(green-main);
			}

			@include above($large-desktop) {
				left: 5rem;
			}
		}
	}
}
