.announcings {
	.slider-block__init {
		@include above($tablet) {
			padding-left: 0;
			padding-right: 0;
		}
	}

	.slider-block__slide {
		@include above($tablet) {
			width: auto;
		}
	}

	.slider-block__arrows {
		display: none;

		@include above($tablet) {
			display: flex;
		}
	}

	.slider-block__dots {
		@include above($tablet) {
			display: none;
		}
	}
}
