html {
	font-size: 62.5%;

	height: 100%;

	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}

* {
	min-width: 0;
	min-height: 0;
}

*,
*::before,
*::after {
	box-sizing: border-box;

	color: inherit;
}

.body {
	font-family: $font-avenir;

	position: relative;

	overflow-x: hidden;

	min-width: 320px;
	height: 100%;

	-webkit-overflow-scrolling: touch;
	-webkit-font-smoothing: antialiased;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);

	// @include ident-level(bottom, blue, padding);
	// @include ident-level(top, blue);
	@include text-base-large();

	&__wrapper {
		position: relative;

		display: flex;
		flex-direction: column;

		min-height: 100%;
	}

	&.scroll-locked {
		position: fixed;
		left: 0;

		overflow: hidden;

		width: 100%;
		height: 100%;
	}
	&.tingle-enabled{
		position: fixed;
	}
}

main {
	position: relative;

	flex-grow: 1;

	min-height: 100%;

	//@include indent-level(padding-bottom, purple);
}

input {
	-webkit-appearance: none;
	-moz-appearance: textfield;
}

button,
input,
textarea {
	font-family: inherit;

	margin: 0;
	padding: 0;

	border: none;
	background: none;

	&:focus {
		outline: 0;
	}
}

a {
	text-decoration: none;
}

b {
	font-weight: $bold-weight;
}

sup {
	font-size: .6em;

	vertical-align: super;
}
