.materials-group {
	&__row {
		//@include indent-level(margin-bottom, green, 'y', true);
	}

	&__col {
		@include indent-level(margin-bottom, green);
	}

	&__link {
		display: block;
	}

	&__link:hover &__img {
		@media (hover: hover) {
			transform: scale(1.1);
		}
	}

	&__img-wrap {
		position: relative;

		overflow: hidden;

		margin-bottom: 1rem;

		@include above($large-desktop) {
			height: 24rem;
		}

		&::after {
			position: relative;

			display: block;

			width: 100%;
			padding-bottom: 100%;

			content: ' ';

			@include above($large-desktop) {
				display: none;
			}
		}
	}

	&__img {
		position: absolute;
		top: 0;
		left: 0;

		width: 100%;
		height: 100%;

		transition: transform #{$transition-anim};

		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
	}

	&--tile {
		position: relative;

		&::before {
			position: absolute;
			z-index: -1;
			top: -5rem;
			left: 0;

			width: 25rem;
			height: 13rem;

			content: '';

			background-color: color(blue-bg);

			@include above($tablet) {
				width: 50%;
			}

			@include above($desktop) {
				height: 20rem;
			}

			@include above($mid-desktop) {
				top: -8rem;

				height: 25rem;
			}

			@include above($large-desktop) {
				top: -10rem;
			}
		}
	}
}
