.inputs-block {
	display: flex;
	flex-direction: column;
	justify-content: center;

	height: 100%;
	padding: 1.8rem 2rem;

	background-color: color(blue-bg);

	@include above($desktop) {
		padding-right: 7rem;
		padding-left: 6rem;
	}

	&__col {
		margin-bottom: 2rem;

		@include above($tablet) {
			margin-bottom: 0;
		}

		@include above($desktop) {
			margin-bottom: 2rem;
		}

		&:last-of-type {
			margin-bottom: 0;
		}
	}
}
