.header {
	//position: sticky;
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	z-index: 500;

	&__wrapper {
		position: relative;
		z-index: 100;

		height: 5rem;

		transition: background-color $transition;

		border-bottom: .1rem solid color(dark-10);
		background-color: color(white-main);

		@include above($desktop) {
			height: 6.8rem;

			border-bottom: none;
			background: transparent;

			@include padding-x(2rem);
		}

		@include above($mid-desktop) {
			height: 9rem;
		}

		&::before {
			@include above($desktop) {
				position: absolute;
				z-index: 0;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;

				display: block;

				content: '';

				background: linear-gradient(
					180deg,
					#ffffff 0%,
					rgba(255, 255, 255, .82) 61.7%,
					rgba(255, 255, 255, 0) 100%
				);
			}
		}

		&:hover {
			@include above($desktop) {
				@media (hover: hover) {
					background-color: color(white-main);
				}
			}
		}

		&.is-active {
			background: color(white-main);
		}
	}

	&__row {
		display: flex;
		align-items: center;
		justify-content: space-between;

		height: 100%;
	}

	&__col {
		display: flex;
		align-items: center;
	}

	&__control-row {
		display: flex;
		align-items: center;

		@include margin-x(-1.25rem);

		> * {
			display: flex;

			@include margin-x(1.25rem);
		}
	}

	&__logo {
		z-index: 1;

		display: flex;
		align-items: center;

		@include above($desktop) {
			margin-right: 5.5rem;
		}

		@include above($large-desktop) {
			margin-right: 8.4rem;
		}

		@include above($big-desktop) {
			margin-right: 15rem;
		}
	}

	&__logo-link {
		height: 4rem;
		width: auto;

		@include above($desktop) {
			margin-right: .5rem;
			margin-bottom: 1.2rem;
		}

		@include above($mid-desktop) {
			height: 5.3rem;
			margin-bottom: 1.7rem;
		}

		img {
			height: 100%;
		}
	}

	&__logo-modal {
		display: none;

		@include above($desktop) {
			display: inline-flex;
		}
	}

	&__menu {
		display: flex;
		align-items: center;

		> * {
			&:not(:last-child) {
				margin-right: 4rem;
			}
		}
	}

	&__lang {
		margin-right: 6.3rem;
	}

	&__only--mobile {
		@include above($desktop) {
			display: none;
		}
	}

	&__only--desktop {
		@include below($desktop - 1px) {
			display: none;
		}
	}

	&__search {
		position: absolute;
		z-index: 1;
		top: 0;
		right: 0;
		left: 0;

		@include above($desktop) {
			top: .9rem;

			margin: 0 25rem;
		}

		@include above($mid-desktop) {
			top: 2rem;
		}

		.search-form {
			overflow: visible;

			@include above($desktop) {
				overflow: hidden;
			}

			&__wrapper {
				@include above($desktop) {
					margin: 0 4rem;
				}
			}

			.search-results {
				position: fixed;
				top: 5.1rem;
				right: 0;
				left: 0;

				max-height: calc(100% - 5.1rem);

				background: color(white-main);

				@include above($desktop) {
					position: relative;
					top: 0;

					max-height: calc(100vh - 6rem);
				}

				@include above($mid-desktop) {
					max-height: calc(100vh - 7.1rem);
				}
			}

			.search-results__wrapper {
				max-height: calc(100% - 5rem);
			}
		}
	}

	&__search-overlay {
		position: fixed;
		z-index: -1;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;

		display: block;

		background: rgba(color('dark-main'), .6);

		@include above($desktop) {
			top: 6.8rem;
		}

		@include above($mid-desktop) {
			top: 9rem;
		}
	}

	&__search-hidden {
		@include above($desktop) {
			visibility: hidden;
		}
	}

	&__search-close {
		display: none;

		@include above($desktop) {
			display: block;
		}
	}

	.search-down-enter-active {
		transition: transform $transition;
	}

	.search-down-leave-active {
		transition: opacity $transition;
	}

	.search-down-enter {
		transform: translateY(-100%);
	}

	.search-down-enter-to {
		transform: translateY(0%);
	}

	.search-down-leave {
		opacity: 1;
	}

	.search-down-leave-to {
		opacity: 0;
	}
}
