.catalog-new-items {
	overflow: hidden;

	&__slider-block {
		@include above($mid-desktop) {
			margin-top: -3rem;
		}
	}

	.slider-block__init {
		padding-left: 0;
		padding-right: 0;

		@include above($large-desktop) {
			padding-left: 3rem;
		}
	}

	.slider-block__slide {
		width: 100%;
		margin: 0 auto;

		@include above($mid-desktop) {
			display: flex;
			width: auto;
		}
	}

	.slider-block__arrows {
		display: none;

		@include above($mid-desktop) {
			display: flex;
		}
	}

	.slider-block__dots {
		@include above($mid-desktop) {
			display: none;
		}
	}

	.swiper-slide {
		@include above($mid-desktop) {
			display: flex;
			width: auto;
			height: auto;
			padding-top: 3rem;
			padding-bottom: 3rem;
		}

		.card-block {
			//border: 1px solid #000;
		}

		&:first-of-type {
			@include above($mid-desktop) {
				padding-left: 4rem;
			}

			@include above($large-desktop) {
				padding-left: 2rem;
			}
		}


		&:last-of-type {
			@include above($large-desktop) {
				padding-right: 2rem;
			}
		}
	}

}
