.jumbotron {
	padding: 2rem;

	background-color: color(blue-bg);

	&--xs {
		padding: .7rem 1.6rem;
	}

	&--sm {
		@include above($desktop) {
			padding: 1.4rem 4rem;
		}
	}

	&--sm-lg {
		padding: 2rem 3rem;

		@include above($desktop) {
			padding: 3rem 4rem;
		}
	}

	&--md {
		@include above($desktop) {
			padding: 3rem 4rem;
		}
	}

	&--lg {
		@include indent-level(padding-top, blue);
		@include indent-level(padding-bottom, blue);

		@include above($desktop) {
			padding-right: 5rem;
			padding-left: 5rem;
		}

		@include above($mid-desktop) {
			padding-right: 8.8rem;
			padding-left: 8.8rem;
		}

		@include above($large-desktop) {
			padding-right: 10rem;
			padding-left: 10rem;
		}

		@include above($big-desktop) {
			padding-right: 12.5rem;
			padding-left: 12.5rem;
		}
	}

	&--xl {
		padding-right: 2rem;
		padding-left: 2rem;

		@include indent-level(padding-top, purple);
		@include indent-level(padding-bottom, purple);

		@include above($desktop) {
			padding-right: 5rem;
			padding-left: 5rem;
		}

		@include above($mid-desktop) {
			padding-right: 8.8rem;
			padding-left: 8.8rem;
		}

		@include above($large-desktop) {
			padding-right: 10rem;
			padding-left: 10rem;
		}

		@include above($big-desktop) {
			padding-right: 12.5rem;
			padding-left: 12.5rem;
		}
	}

	&--bg-gray {
		background-color: color(dark-bg);
	}

	&--overflow {
		margin-right: -2rem;
		margin-left: -2rem;

		@include above($tablet) {
			margin-right: 0;
			margin-left: 0;
		}
	}

	&--regular {
		padding: 2rem;
	}

	&--middle {
		padding: 3rem;
	}
}
