.nav-section {
	&__list {
		@include above($mid-desktop) {
			padding-right: 5.8rem;
		}
	}

	&__item {
		position: relative;

		padding-left: 1.7rem;

		@include indent-level(margin-bottom, yellow);

		&::before {
			position: absolute;
			top: .9rem;
			left: 0;

			width: .6rem;
			height: .6rem;

			content: '';
			transition: all #{$transition};
			transform: translateX(-50%);

			-webkit-border-radius: 50%;
			-moz-border-radius: 50%;
			border-radius: 50%;
			background-color: color(green-main);
		}

		&::after {
			position: absolute;
			top: 13px;
			left: 4px;

			width: .8rem;
			height: .2rem;

			content: '';
			transition: all #{$transition};

			opacity: 0;
			border-radius: 1rem;
			background-color: color(green-main);
		}

		&:last-of-type {
			margin-bottom: 0;
		}

		&.is-visible {
			&::before {
				width: 1rem;
				height: 1rem;

				border: 2px solid color(green-main);
				background-color: #ffffff;
			}

			&::after {
				opacity: 1;
			}

			~ .is-visible {
				&::before {
					width: .6rem;
					height: .6rem;

					border-width: 0;
					background-color: color(green-main);
				}

				&::after {
					opacity: 0;
				}
			}
		}
	}

	&__item:not(.is-visible) &__link {
		&:hover {
			transform: translateX(1rem);
		}
	}

	&__link {
		display: inline-flex;

		transition: color #{$transition}, transform #{$transition};
		cursor: pointer;
	}

	&__bottom {
		margin-top: 3.2rem;

		&.not-in-visible {
			transform: translateX(0);

			&.is-visible {
				transform: translateX(-1rem);
			}
		}
	}

	&__btn {
		margin-left: -1.9rem;
	}

	.is-visible &__link {
		font-weight: 700;

		color: color(dark-main);
	}

	.is-visible ~ .is-visible &__link {
		font-weight: 400;

		color: color(dark-55);
	}
	.is-only-last-child .is-visible &__link {
		font-weight: 400;

		color: color(dark-55);

	}
	.is-only-last-child &__item.is-visible {

		&::before {
			width: .6rem;
			height: .6rem;

			border-width: 0;
			background-color: color(green-main);
		}

		&::after {
			opacity: 0;
		}

	}
	.is-only-last-child .is-visible:last-child &__link {
		font-weight: 700;

		color: color(dark-main);
	}
	.is-only-last-child &__item.is-visible:last-child {
		&::before {
			width: 1rem;
			height: 1rem;

			border: 2px solid color(green-main);
			background-color: #ffffff;
		}

		&::after {
			opacity: 1;
		}
	}
}
