.custom-select {
	position: relative;

	&__list {
		position: absolute;
		z-index: 10;
		top: 100%;
		left: 0;

		visibility: hidden;

		width: 26rem;

		transition: opacity #{$transition}, visibility #{$transition};

		opacity: 0;
		background-color: #ffffff;
		box-shadow: $base-shadow;

		&.is-active {
			visibility: visible;

			opacity: 1;
		}
	}

	&__select {
		position: absolute;
		top: 0;
		left: 0;

		display: block;

		pointer-events: all;

		opacity: 0;

		@include size(100%);

		@media (hover: hover) {
			pointer-events: none;
		}
	}

	&__option-link {
		position: relative;

		width: 100%;
		padding: 1rem 4rem 1rem 2rem;

		&::before {
			right: 2rem;

			display: none;

			width: 1.4rem;
			height: .9rem;

			content: '';

			background: url("data:image/svg+xml,%3Csvg viewBox='0 0 14 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M4.91545 6.91125L11.5756 0.251053C11.9104 -0.0836842 12.4531 -0.0836842 12.7878 0.251053C13.1225 0.58579 13.1225 1.12851 12.7878 1.46324L5.62589 8.62518C5.59787 8.66497 5.56607 8.7029 5.53049 8.73849C5.33161 8.93737 5.0593 9.01809 4.80083 8.98064C4.61865 8.95767 4.44243 8.87624 4.30254 8.73635C4.27154 8.70535 4.24341 8.67256 4.21815 8.63833L0.251051 4.6712C-0.0836832 4.33646 -0.0836838 3.79374 0.25105 3.45901C0.585784 3.12427 1.12849 3.12427 1.46323 3.45901L4.91545 6.91125Z' fill='%23423B57'/%3E%3C/svg%3E%0A")
				no-repeat center center / cover;

			@include center($horiz: false);
		}

		&.is-selected {
			background-color: color(orange-light);

			&::before {
				display: block;
			}
		}
	}

	&--theme--blue &__option-link {
		&:hover {
			@media (hover: hover) {
				color: color(monochrome-main);
				background-color: color(monochrome-thin);
			}
		}

		&.is-selected {
			background-color: color(blue-light);

			@media (hover: hover) {
				background-color: color(blue-light);
			}
		}
	}
}
