.nav {
	&--mobile {
		z-index: 100;

		margin-right: -2rem;
		margin-left: -2rem;
		padding: 1rem 1.5rem;

		background: color(white-main);

		.is-fixed & {
			box-shadow: 0 .4rem .8rem rgba(color(dark-main), .1);
		}
	}

	&--mobile &__list {
		flex-wrap: nowrap;
		overflow: auto;
		position: relative;
		margin-right: -2rem;

		@include above($desktop) {
			margin-right: 0;
		}
	}

	&--mobile &__link {
		padding: .7rem 1.5rem .6rem;
		//border: none;
		height: 3.4rem;
	}

	&--mobile &__item:last-of-type {
		padding-right: 2rem;
	}

	&--mobile-full &__list {
		margin-left: -2rem;

		@include above($mid-desktop) {
			overflow: visible;
			flex-wrap: wrap;

			margin-bottom: -1rem;
		}
	}

	&--mobile-full &__item:first-of-type {
		padding-left: 2rem;

		@include above($mid-desktop) {
			padding-left: .5rem;
		}
	}

	&--mobile-materials &__item:first-of-type {
		@include above($desktop) {
			padding-left: 6.5rem;
		}
	}

	&--mobile-materials &__item:last-of-type {
		@include above($desktop) {
			padding-right: 6.5rem;
		}
	}

	&--mobile-materials &__list {
		@include above($desktop) {
			margin-left: -6.5rem;
			margin-right: -6.5rem;
		}

		@include above($mid-desktop) {
			margin-left: -.5rem;
			margin-right: -.5rem;
		}
	}

	&--mobile-full &__item {
		@include above($mid-desktop) {
			margin-bottom: 1rem;
		}
	}

	&--mobile-full &__item:last-of-type {
		@include above($mid-desktop) {
			padding-right: .5rem;
		}
	}

	&--mobile-full &__link {
		align-items: center;

		width: auto;
		max-width: 19rem;
		height: 100%;
		min-height: 8.2rem;

		@include above($mid-desktop) {
			max-width: none;
			min-height: 3.2rem;
		}
	}

	&--mobile-full &__link .button__text {
		white-space: normal;

		@include above($mid-desktop) {
			white-space: nowrap;
		}
	}

	&--no-wrap &__link {
		max-width: none;
		min-height: auto;

		white-space: nowrap;
	}

	&--bg-transparent {
		background-color: transparent;
	}

	&--mobile-services {
		margin-left: -1.5rem;
		margin-right: -1.5rem;
	}

	&--mobile-services &__list {
		overflow: visible;
		flex-wrap: wrap;

		margin-left: .5rem;
		margin-right: .5rem;
		margin-bottom: -1rem;
	}

	&--mobile-services &__item {
		margin-bottom: 1rem;
		width: 100%;

		@include above($tablet) {
			width: 30.5rem;
		}

		@include above($desktop) {
			width: auto;
		}

		&:first-of-type {
			padding-left: .5rem;
		}

		&:last-of-type {
			padding-right: .5rem;
		}
	}

	&--mobile-services &__link {
		justify-content: flex-start;

		width: 100%;
		max-width: none;

		text-align: left;

		@include above($desktop) {
			max-width: none;
			min-height: 3.2rem;
		}
	}

	.is-visible &__link {
		background-color: color(green-main);
		color: color(white-main);
	}

	.is-visible ~ .is-visible &__link {
		background-color: color(blue-sky);
		color: color(dark-main);
	}
	
	.is-only-last-child .is-visible &__link {
		background-color: color(blue-sky);
		color: color(dark-main);
	
	}
	.is-only-last-child .is-visible:last-child &__link {
		background-color: color(green-main);
		color: color(white-main);
	}
}
