.mobile-menu {
	position: fixed;
	z-index: 500;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	transition: transform $transition, opacity $transition;
	transform: translateX(-100%);
	pointer-events: none;

	opacity: 0;
	background: color(white-main);

	@include above($tablet) {
		width: 37.5rem;
	}

	&__wrapper {
		position: relative;

		display: flex;
		flex-direction: column;

		height: 100%;
	}

	&__body {
		flex: 1;
		min-height: 0;
		position: relative;
		background: color(white-main);
	}
	&__body--bg--blue {
		background: color(blue-bg);
	}
	&__container {
		height: 100%;
		width: 100%;
		display: flex;
		overflow-y: auto;
		flex-direction: column;
	}
	&__group {
		flex-shrink: 0;
		background: color(white-main);
		padding: 1rem 0;

		&:not(:last-child) {
			&::after {
				display: block;

				width: calc(100% - 4rem);
				height: .1rem;
				margin: 0 2rem;

				content: '';
				transform: translateY(1rem);

				border: .1rem solid color(dark-10);
			}
		}
	}

	&__group--bg--blue {
		background: color(blue-bg);
	}

	&__group--no-border {
		&:not(:last-child) {
			&::after {
				display: none;
			}
		}
	}

	&__head {
		position: relative;
		z-index: 10;

		height: 5rem;

		background: color(blue-bg);
	}

	&__head-title {
		display: flex;
		align-items: center;
		justify-content: center;

		height: 100%;
	}

	&__back,
	&__close {
		position: absolute;
		top: 50%;

		display: flex;

		transform: translateY(-50%);
	}

	&__back {
		left: 1.5rem;
	}

	&__close {
		right: 1.5rem;
	}

	&__item {
		display: flex;
		flex-direction: column;
		justify-content: center;

		min-height: 4.5rem;
		padding: .5rem 2rem;
	}

	&__item--blue {
		background: color('blue-bg');
	}

	&__link {
		flex-shrink: 0;

		width: 100%;
		height: 100%;

		.link__counter {
			top: 50%;
			right: 0;
			left: auto;

			min-width: 2.4rem;
			height: 2.4rem;
			padding: 0 .4rem;

			transform: translateY(-50%);

			background-color: color('green-main');

			@include text-base-small();
		}

		.link__icon {
			margin-right: 2rem;
		}
	}

	&__link--parent {
		position: relative;

		padding-right: 5rem;

		&::before {
			position: absolute;
			top: 50%;
			right: 0;

			display: block;

			content: '';
			transform: translateY(-50%);

			background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M11.42 8.41992L5.41999 14.4199L4.57999 13.5799L9.73999 8.41992L4.57999 3.25992L5.41999 2.41992L11.42 8.41992Z' fill='%2307150A' stroke='%2307150A' stroke-width='0.5'/%3E%3C/svg%3E%0A");
			background-repeat: no-repeat;
			background-size: contain;

			@include size(1.6rem);
		}
	}

	&__children {
		position: absolute;
		z-index: 10;
		top: 0;
		left: 0;

		width: 100%;
		height: 100%;

		transform: translateX(-100%);

		background-color: #ffffff;
		box-shadow: 0 .6rem 2.5rem rgba(25, 72, 97, .13);

		&.is-opened {
			transform: translateX(0);
		}
	}

	&__lang {
		> * {
			&:not(:last-child) {
				margin-right: 2rem;
			}
		}
	}

	&__footer {
		flex-shrink: 0;

		margin-top: auto;

		background-color: inherit;
	}

	&__footer--sticky {
		position: sticky;
		z-index: 1;
		bottom: 0;
		box-shadow: 0 0 1.5rem -1.2rem color(dark-55);
	}

	&.is-opened {
		transform: translateX(0);
		pointer-events: all;

		opacity: 1;
	}

	.slide-right-enter-active,
	.slide-right-leave-active {
		transition: transform $transition;
	}

	.slide-right-enter {
		transform: translateX(-100%);
	}

	.slide-right-enter-to {
		transform: translateX(0%);
	}

	.slide-right-leave {
		transform: translateX(-100%);
	}

	.slide-right-leave-to {
		transform: translateX(-100%);
	}
}
