.custom-table {
	position: relative;

	&::after {
		position: absolute;
		z-index: 3;
		top: 0;
		bottom: 0;
		left: 18rem;
		opacity: 0;
		width: 2rem;
		transition: opacity #{$transition};
		content: '';

		background: linear-gradient(90deg, rgba(0, 0, 0, .03) 0%, rgba(0, 0, 0, .001) 100%);

		@include above($desktop) {
			left: 41rem;
		}
	}

	&__shadow {
		position: absolute;
		z-index: 4;
		top: 4.3rem;
		right: 0;
		left: 0;

		height: 2rem;

		transition: opacity #{$transition};
		pointer-events: none;

		opacity: 0;
		background: linear-gradient(to bottom, rgba(0, 0, 0, .03) 0%, rgba(0, 0, 0, .001) 100%);
	}

	&__wrap {
		overflow: auto;

		max-width: 100%;
		height: 100vh;
	}

	&__inner {
		position: relative;
		z-index: 0;

		display: table;

		width: 100%;
	}

	&__thead {
		z-index: 3;
		position: relative;

		display: table-header-group;
	}

	&__thead &__th {
		&:first-of-type {
			display: table-cell;
		}

		&:nth-of-type(2) {
			display: none;

			@include above($desktop) {
				display: table-cell;
			}
		}
	}

	&__tbody {
		display: table-row-group;
	}

	&__tfoot {
		display: table-footer-group;
	}

	&__tr {
		display: table-row;

		&--hidden {
			width: 100%;

			background-color: color(blue-sky);

			@include above($desktop) {
				display: none;
			}
		}
	}

	&__tr--hidden &__td {
		background-color: color(blue-sky);

		&:first-of-type {
			z-index: 2;

			display: table-cell;

			border-bottom: .1rem solid color(dark-10);
		}

		&:nth-of-type(2) {
			width: 11rem;
			max-width: 11rem;
			min-width: 11rem;
		}
	}

	&__tr--top &__td {
		&::before {
			@include above($desktop) {
				position: absolute;
				top: .1rem;
				left: 0;

				width: 100%;
				height: .1rem;

				content: '';

				background-color: color(dark-10);
			}
		}

		&:first-of-type {
			&::after {
				@include above($desktop) {
					position: absolute;
					top: -.1rem;
					left: 0;

					width: 100%;
					height: .1rem;

					content: '';

					background-color: color(dark-10);
				}
			}
		}
	}

	&__tr:hover &__td {
		@media (hover: hover) {
			background-color: color(blue-bg);
		}
	}

	&__th,
	&__td {
		position: sticky;
		top: 0;

		display: table-cell;

		width: 11rem;
		min-width: 11rem;
		max-width: 11rem;
		padding: 1.2rem 1rem;

		vertical-align: middle;

		background-color: color(white-main);

		&:first-of-type {
			position: sticky;
			z-index: 1;
			left: 0;

			display: none;

			@include above($desktop) {
				display: table-cell;
			}
		}

		&:nth-of-type(2) {
			z-index: 1;
			left: 0;

			width: 18rem;
			min-width: 18rem;
			max-width: 18rem;

			@include above($desktop) {
				left: 11rem;

				width: 30rem;
				min-width: 30rem;
				max-width: 30rem;
			}

			//&:after {
			//	content: '';
			//	position: absolute;
			//	left: 100%;
			//	top: -.1rem;
			//	bottom: -.1rem;
			//	width: 20px;
			//	background: linear-gradient(90deg, rgba(0, 0, 0, .04) 0%, rgba(0, 0, 0, .001) 100%);
			//}
		}
	}

	&__th {
		border-top: .1rem solid color(dark-10);
		border-bottom: .1rem solid color(dark-10);
	}

	&__td {
		transition: background-color #{$transition};

		&:not(:first-of-type) {
			border-bottom: .1rem solid color(dark-10);
		}

		&-row {
			display: flex;

			width: 100%;
		}

		&-col {
			display: inline-flex;
			align-items: center;

			width: 50%;
			min-width: 50%;
			max-width: 50%;
			padding-right: .5rem;
			padding-left: .5rem;

			text-align: left;

			//&:first-of-type {
			//	width: 60%;
			//	max-width: 60%;
			//	min-width: 60%;
			//}
			//
			//&:nth-of-type(2) {
			//	width: 40%;
			//	max-width: 40%;
			//	min-width: 40%;
			//}
		}
	}

	&.is-scroll-y &__shadow {
		opacity: 1;
	}
	&.is-scroll-x::after {
		opacity: 1;
	}
}
