.tech-block {
	display: flex;

	&__link {
		@include above($tablet) {
			align-items: flex-start;
		}
	}

	&__link &__icon:not(:only-child) {
		margin-left: 0;
	}

	&__link:hover .link__text {
		text-decoration: underline;
	}

	&__icon {
		margin-right: 1rem;
	}

	&__info {
		align-items: center;
	}

	&__title {
		@include above($tablet) {
			margin-bottom: .5rem;
		}
	}

	&__text {
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
		overflow: hidden;
		text-align: left;
	}

	&__desc {
		margin-left: .2rem;
		flex-shrink: 0;
		text-align: left;

		color: color(dark-40);

		@include above($tablet) {
			display: block;

			margin-top: .5rem;
		}
	}

	&--dark:hover .link__text {
		text-decoration: none;

		color: color(green-hover);
	}

	&--dark .link__text {
		color: color(dark-main);
	}

	&--col &__info {
		flex-direction: column;
		align-items: flex-start;
	}
}
