.publication-card {
	display: block;

	&:hover &__img {
		@media (hover: hover) {
			transform: translate(-50%, -50%) scale(1.1);
		}
	}

	&__top {
		position: relative;

		@include indent-level(margin-bottom, orange);

		&::before {
			display: block;

			width: 100%;
			padding-bottom: 61%;

			content: '';
		}
	}

	&__icon {
		position: absolute;
		z-index: 1;
		top: 50%;
		left: 50%;

		transform: translate(-50%, -50%);
	}

	&__img-wrap {
		position: absolute;
		top: 0;
		left: 0;

		overflow: hidden;

		width: 100%;
		height: 100%;
	}

	&__img {
		position: absolute;
		top: 50%;
		left: 50%;

		width: 100%;
		height: 100%;

		transition: transform #{$transition-anim};
		transform: translate(-50%, -50%);

		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
	}

	&__title {
		@include indent-level(margin-bottom, red);
	}

	&__desc {
		line-height: 1.3;
	}

	&--slide-sm {
		width: 24rem;

		@include above($tablet) {
			width: 21rem;
		}

		@include above($desktop) {
			width: 100%;
		}
	}

	&--sm &__top {
		@include above($tablet) {
			//height: 30rem;
		}

		@include above($mid-desktop) {
			//height: 32rem;
		}

		@include above($large-desktop) {
			//height: 34rem;
		}

		@include above($big-desktop) {
			//height: 37rem;
		}

		&::before {
			padding-bottom: 142%;
		}
	}

	&--square &__top {
		&::before {
			padding-bottom: 100%;
		}
	}
}
