.card-block {
	position: relative;

	display: block;

	padding-right: 2rem;
	padding-left: 2rem;

	transition: box-shadow #{$transition};

	border: .1rem solid color(dark-10);
	background-color: color(white-main);

	@include indent-level(padding-bottom, green);
	@include indent-level(padding-top, green);

	@include above($mid-desktop) {
		padding-right: 4rem;
		padding-left: 4rem;
	}

	&:hover {
		@media (hover: hover) {
			z-index: 1;

			box-shadow: 0 .3rem 3rem rgba(7, 21, 10, .1);
		}
	}

	&__title {
		text-align: left;
	}

	&__col--2 {
		@include above($mid-desktop) {
			width: 100%;
		}
	}

	&__img {
		width: 100%;
		max-width: 15rem;
		margin: 0 auto 1rem;

		img {
			width: auto;
			max-width: 100%;
		}
	}

	&__inner {
		display: flex;
		flex-direction: column;
		position: relative;
		height: 100%;
	}

	&__author {
		flex: 1;
		margin-bottom: 2rem;
	}

	&--news-catalog &__inner {
		@include above($tablet) {
			display: flex;
			align-items: center;
		}

		@include above($mid-desktop) {
			flex-direction: column;

			height: 100%;
		}
	}

	&--news-catalog &__header {
		display: block;
	}

	&--news-catalog &__header-top {
		display: flex;
		align-items: flex-start;
		flex-wrap: wrap;

		@include indent-level(margin-bottom, red);

		@include above($mid-desktop) {
			flex-direction: column-reverse;
			position: relative;
			padding-top: 3.4rem;
		}
	}

	&--news-catalog &__header-bottom {
		@include above($mid-desktop) {
			//height: 5.8rem;
			display: -webkit-box;
			-webkit-line-clamp: 3;
			-webkit-box-orient: vertical;
			overflow: hidden;
		}
	}

	&--news-catalog &__title {
		padding-right: 1rem;

		@include above($mid-desktop) {
			padding-right: 0;

			@include indent-level(margin-bottom, orange);
		}
	}

	&--news-catalog &__col--2{
		margin-top: auto;
	}

	&--news-catalog &__bottom {
		@include above($mid-desktop) {
			min-height: 7.3rem;
		}
	}

	&--news-catalog &__labels {
		@include above($mid-desktop) {
			position: absolute;
			top: 0;
			left: 0;
		}
	}

	&--news-catalog &__col--1 {
		@include indent-level(margin-bottom, orange);

		@include above($tablet) {
			width: 100%;
			max-width: 55%;
			margin-bottom: 0;
			padding-right: 1.5rem;
		}

		@include above($mid-desktop) {
			max-width: none;
			margin-bottom: 2.2rem;
			padding-right: 0;
		}
	}

	&--news-catalog &__bottom {
		@include above($mid-desktop) {
			border-top: .1rem solid color(dark-10);

			@include indent-level(padding-top, orange);
		}
	}

	&--slide {
		border-top: none;
		border-right: none;
		border-left: none;

		@include above($desktop) {
			padding-right: 6rem;
			padding-left: 6rem;
		}

		@include above($mid-desktop) {
			width: 30rem;
			min-width: 30rem;

			border: none;
		}

		&:first-of-type {
			border-top: .1rem solid color(dark-10);

			@include above($mid-desktop) {
				border: none;
			}
		}
	}

	&--slide &__col--2 {
		@include above($tablet) {
			margin-top: 0;
			width: 25rem;
			margin-left: auto;
		}

		@include above($mid-desktop) {
			width: 100%;
			margin-top: auto;
		}
	}

	&--news {
		width: 30rem;
		height: 100%;

		@include above($desktop) {
			width: 100%;
		}

		@include above($mid-desktop) {
			padding: 3rem;
		}

		@include above($large-desktop) {
			padding-right: 4rem;
			padding-left: 4rem;
		}
	}

	&--news &__inner {
		min-height: 19rem;
	}

	&--news &__labels {
		@include indent-level(margin-bottom, yellow);
	}

	&--news &__desc {
		@include indent-level(margin-bottom, orange);
	}

	&--trainings {
		height: auto;

		transition: background-color #{$transition};

		@include above($mid-desktop) {
			@include indent-level(padding-bottom, green);
			@include indent-level(padding-top, green);
		}

		&:hover {
			@media (hover: hover) {
				background-color: color(green-main);
				box-shadow: none;
			}
		}
	}

	&--trainings:hover .dates {
		@media (hover: hover) {
			color: #ffffff;
		}
	}

	&--trainings:hover &__desc {
		@media (hover: hover) {
			color: #ffffff;
		}
	}

	&--trainings:hover .link__text {
		@media (hover: hover) {
			color: #ffffff;
		}
	}

	&--trainings &__inner {
		display: flex;
		flex-direction: column;

		min-height: 28rem;

		@include above($mid-desktop) {
			min-height: 31rem;
		}

		@include above($large-desktop) {
			min-height: 30rem;
		}
	}

	&--trainings &__dates {
		@include indent-level(margin-bottom, green);
	}

	&--trainings .link__text {
		margin-top: auto;
	}

	&--materials {
		margin-right: -2rem;
		margin-left: -2rem;

		@include above($desktop) {
			margin-right: 0;
			margin-left: 0;
		}

		@include above($mid-desktop) {
			width: 100%;
			height: 100%;
			padding-right: 3rem;
			padding-left: 3rem;
			padding-top: 3.5rem;
			padding-bottom: 3.5rem;
		}

		@include above($large-desktop) {
			padding-top: 3.5rem;
			padding-bottom: 3.5rem;
		}
	}

	&--materials &__inner {
		@include above($mid-desktop) {
			min-height: 19rem;
		}
	}

	//&--materials &__col--2{
	//	@include above($mid-desktop) {
	//		width: 100%;
	//	}
	//}

	&--materials &__col--1 {
		@include above($mid-desktop) {
			margin-bottom: 1rem;
		}
	}

	&--materials .tech-block__link {
		@include above($mid-desktop) {
			align-items: center;
		}
	}

	&--materials .tech-block__desc {
		@include above($mid-desktop) {
			margin-top: 0;
		}
	}

	&--materials:first-of-type {
		border-bottom: 0;
	}

	&--vebinars {
		//border-color: transparent;
	}

	&--no-border {
		border: none;
	}

	&--brand &__inner {
		text-align: center;
	}

	&--no-hover {
		&:hover {
			@media (hover: hover) {
				box-shadow: none;
			}
		}
	}

	&--request {
		width: 100%;
	}

	&--request &__header-top {
		padding-right: 1.6rem;
		@include above($mid-desktop) {
			flex-direction: row;
			padding-top: 0;
		}
	}

	&--request &__title {
		@include above($mid-desktop) {
			margin-bottom: 0;
		}
	}

	&--request &__labels {
		@include above($mid-desktop) {
			margin-bottom: 0;
			padding-left: .8rem;
			position: static;
		}
	}

	&--request &__inner {
		@include above($tablet) {
			display: block;
		}
	}

	&--request &__col--1 {
		@include above($tablet) {
			width: 100%;
			max-width: none;
			margin-bottom: 1rem;
			padding-right: 0;
		}

		@include above($mid-desktop) {
			margin-bottom: 2rem;
		}
	}

	&--request &__bottom {
		@include above($mid-desktop) {
			padding-top: 0;

			border-top: none;

			min-height: 0;
		}
	}

	&--request .tech-block__link {
		align-items: center;
	}

	&--request .tech-block__desc {
		margin-top: 0;
	}

	&--request .tech-block__info {
		display: flex;
	}

	&--w-100 {
		width: 100%;
	}
}
