.table {
	overflow:  auto;
	text-align: left;
	@extend .t-base--size--small;
	$this: &;

	.ps__rail-x, .ps__rail-y {
		z-index: 3;
	}

	&.active-y {
		& > table {
			& > thead {
				& > tr {
					& > th {
						box-shadow: 0px 3px 30px rgba(7, 21, 10, 0.1);
						clip-path: inset(0px -100vw 0px 0px);


						&:first-child {
							box-shadow: 0px 3px 30px rgba(7, 21, 10, .1%);
							clip-path: inset(0px -100vw 0px 0px);

						}
					}
				}
			}
		}
	}

	&.active-x {
		& > table {

			& > thead {
				& > tr {
					& > th {
						&:first-child {
							box-shadow: 0px 3px 30px rgba(7, 21, 10, .1%);
							clip-path: inset(0px -100vw 0px 0px);
						}
					}
				}
			}

			& > tbody {
				& > tr {
					& > th {
						&:first-child() {
							box-shadow: 0px 3px 30px rgba(7, 21, 10, .1%);
							clip-path: inset(0px -100vw 0px 0px);
						}
					}

					&.heading-row {
						& > th {
							& > table {
								& > tbody {
									& > tr {
										& > td {
											&:first-child {
												&::after {
													@include below($tablet - 1) {
														content: "";
														left: 0;
														top: 0;
														width: 100%;
														height: 100%;
														position: absolute;
														z-index: -1;
														box-shadow: 0px 3px 30px rgba(7, 21, 10, .1%);
														clip-path: inset(0px -100vw 0px 0px);
													}
												}
											}
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}

	table {
		margin: 0;
		border: none;
		border-collapse: separate;
		border-spacing: 0;
		table-layout: fixed;
		min-width: 100%;

		th, td {
			padding: 1.2rem 1rem;
			vertical-align: middle;
		}

		td {
			border-bottom: .1rem solid #e6e8e7;
		}


		tr {
			&:hover {
				@media (hover: hover) {
					background-color: #F4F9FA;

					& > th {
						background-color: #F4F9FA;
					}
				}
			}
		}

	}

	& > table {
		// white-space: nowrap;

		& > thead {
			position: relative;
			z-index: 2;

			& > tr {

				& > th {
					position: sticky;
					top: 0;
					z-index: 1;
					background-color: #fff;
					font-weight: bold;
					border-top: .1rem solid #e6e8e7;
					border-bottom: .1rem solid #e6e8e7;
					white-space: nowrap;

					@include below($tablet - 1) {
						padding-top: 2.2rem;
					}
		
					&:first-child {
						z-index: 2;
						left: 0;
					}
				}
			}
		}
	
		& > tbody {
			& > tr {
				& > th {
					position: sticky;
					left: 0;
					z-index: 1;
					background-color: #fff;
					padding: 0;
					border-bottom: .1rem solid #e6e8e7;

					& > table {
						// white-space: normal;

						& > tbody {
							& > tr {
								display: flex;
	
								& > td {
									flex: 1;
									display: block;
									min-width: 100px;
									border: none;

									// @include above($tablet) {
									// 	min-width: 160px;
									// }
									

									&:first-child {
										display: none;

										@include above($tablet) {
											min-width: 0;
											width: 100px;
											border-bottom: none;
											flex: 0 1 auto;
											display: block;
										}

									}
								}
							}
						}
					}
				}

				&.heading-row {
					@include below($tablet - 1) {
						position: relative;
					}

					& > td {
						&:not(:first-child) {
							@include below($tablet - 1) {
								padding-top: 55px;
							}
						}
					}

					& > th {
						& > table {
							& > tbody {
								& > tr {
									& > td {
										&:not(:first-child) {
											@include below($tablet - 1) {
												padding-top: 55px;
											}
										}
			
										&:first-child {
											@include below($tablet - 1) {
												position: absolute;
												display: block;
												min-width: 0;
												flex: 0 1 auto;
												width: 100%;
											}

											&::before {
												@include below($tablet - 1) {
													content: "";
													left: 0;
													top: 0;
													width: 100vw;
													height: 100%;
													position: absolute;
													background-color: #F4F9FA;
													z-index: -1;
												}
											}
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}

	.double-border {
		& > td, & > th{
			@include above($tablet) {
				border-bottom: 3px double #e6e8e7!important;
			}
		}
	}

}