.products-list {
	display: flex;
	flex-wrap: wrap;

	margin-bottom: -1rem;

	&__item {
		@include above($mid-desktop) {
			margin-bottom: 1rem;
		}
	}

	&__item:last-of-type {
		.card-block--materials {
			border-bottom: 1px solid color(dark-10);

			@include above($mid-desktop) {
				border-bottom: none;
			}
		}
	}

	&--type-list {
		margin-bottom: 0;
	}

	&--type-list &__item {
		@include above($mid-desktop) {
			width: 100%;
		}
	}

	&--type-list .card-block {
		@include above($mid-desktop) {
			padding-left: 3rem;
			padding-right: 3rem;
			padding-top: 2rem;
			padding-bottom: 2rem;
		}
	}

	&--type-list .card-block--materials .card-block__inner {
		@include above($tablet) {
			flex-direction: row;

			height: auto;
			min-height: 5rem;
		}
	}

	&--type-list .card-block--materials .card-block__bottom {
		@include above($mid-desktop) {
			position: relative;

			padding-top: 0;
			padding-left: 4rem;

			border-top: none;

			min-height: auto;
		}

		&::before {
			@include above($mid-desktop) {
				position: absolute;
				top: 50%;
				left: 0;

				width: 1px;
				height: 50px;

				content: '';
				transform: translateY(-50%);

				background-color: color(dark-10);
			}
		}
	}

	&--type-list .card-block--materials .card-block__col--1 {
		@include above($mid-desktop) {
			width: auto;
			margin-bottom: 0;
			max-width: 50%;
		}
	}

	&--type-list .card-block--materials .card-block__col--2 {
		margin-top: 0;
		@include above($mid-desktop) {
			width: 32%;
			margin-left: auto;
		}

		@include above($big-desktop) {
			width: 23%;
		}
	}

	&--type-list .card-block--materials .card-block__header-top {
		@include above($mid-desktop) {
			flex-direction: row;
			padding-top: 0;
		}
	}

	&--type-list .card-block--materials .card-block__title {
		@include above($mid-desktop) {
			margin-right: 10px;
			margin-bottom: 0;
		}
	}

	&--type-list .card-block--materials .card-block__labels {
		@include above($mid-desktop) {
			margin-bottom: 0;
			position: static;
		}
	}

	&--type-list .card-block--materials .card-block__info {
		@include above($mid-desktop) {
			display: flex;
			align-items: center;
		}
	}

	&--type-list .card-block--materials .card-block__desc {
		@include above($mid-desktop) {
			margin-top: 0;
			//margin-left: .5rem;
		}
	}

	&--type-list .card-block--materials .card-block__link {
		@include above($mid-desktop) {
			align-items: center;
		}
	}

	&--type-list .card-block--materials .tech-block__info {
		@include above($mid-desktop) {
			display: flex;
		}
	}

	&--type-list .card-block--materials .tech-block__desc {
		@include above($mid-desktop) {
			margin-left: .5rem;
		}
	}



}
