.sale-table {
	&__row {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		justify-content: space-between;

		margin-right: -1rem;
		margin-left: -1rem;
		padding: 2rem 1rem;

		border-bottom: .1rem solid color(dark-10);

		@include above($desktop) {
			flex-wrap: nowrap;
		}
	}

	&__row--thead {
		padding-bottom: 1rem;

		border-top: .1rem solid color(dark-10);
	}

	&__row--thead &__col--2 {
		display: none;

		@include above($desktop) {
			display: block;
		}
	}

	&__col {
		padding-left: 1rem;
		padding-right: 1rem;
	}

	&__col--1 {
		@include above($desktop) {
			width: 16rem;
			min-width: 16rem;
		}
	}

	&__col--2 {
		order: 5;

		@include above($desktop) {
			flex-grow: 1;
			flex-shrink: 1;
			order: 0;

			width: auto;
		}
	}

	&__col--3 {
		@include above($desktop) {
			width: 15rem;
			min-width: 15rem;
		}
	}

	&--sm &__row--body{

		border-bottom: none;
		border-top: .1rem solid color(dark-10);

		@include above($desktop) {
			border-top: none;
			border-bottom: .1rem solid color(dark-10);
		}
	}

	&--sm &__row {
		&:last-of-type {
			border-bottom: .1rem solid color(dark-10);
		}
	}

	&--sm {

	}
}
