@import './config';

.checkbox {
	display: flex;
	align-items: baseline;

	cursor: pointer;

	color: $checkbox-color;

	@include text-base-medium;

	&:hover &__icon {
		@include above($desktop) {
			@media (hover: hover) {
				box-shadow: $checkbox-hover-shadow;
			}
		}
	}

	&__icon {
		display: flex;
		flex-shrink: 0;

		width: 1.8rem;
		height: 1.8rem;
		margin-right: .7rem;

		transition: background-color #{$transition}, box-shadow #{$transition};

		background: $checkbox-icon-bg-color;
		box-shadow: $checkbox-shadow;

		.icon {
			display: block;
			visibility: hidden;

			width: 1.2rem;
			height: .9rem;

			margin-top: .5rem;
			margin-left: .4rem;

			opacity: 0;
		}
	}

	&.is-disabled {
		pointer-events: none;

		color: $checkbox-disabled-color;
	}

	.is-disabled &__icon {
		background: $checkbox-disabled-bg-color;
	}

	input {
		@include hidden;

		&:checked {
			& ~ .checkbox__icon {
				background: $checkbox-checked-bg-color;
				box-shadow: $checkbox-checked-shadow;

				svg {
					visibility: visible;

					opacity: 1;
				}
			}
		}

		&.error {
			& ~ .checkbox__icon {
				box-shadow: $checkbox-error-shadow;
			}
		}
	}

	label.error {
		display: none !important;
	}
}
