.leaf-item {
	&__inner {
		display: flex;

		@include above($tablet) {
			padding-right: 2rem;
		}

		@include above($desktop) {
			padding-right: 3rem;
		}

		@include above($large-desktop) {
			padding-right: 4rem;
		}

		@include above($big-desktop) {
			padding-right: 11rem;
		}
	}

	&__img {
		width: 2.6rem;
		min-width: 2.6rem;

		transform: translateY(-.4rem);
	}

	&__col--1 {
		flex-shrink: 0;

		margin-right: 1.2rem;
	}
}
