.question-card {
	padding: 2rem;

	border: .1rem solid color(dark-10);
	background-color: color(white-main);

	@include above($mid-desktop) {
		padding: 4rem 4rem 3rem;
	}

	&__top {
		position: relative;

		display: flex;
		align-items: center;

		@include indent-level(margin-bottom, orange);

		@include above($mid-desktop) {
			padding-left: 5rem;
		}
	}

	&__body {
		@include above($mid-desktop) {
			padding-left: 5rem;
		}
	}

	&__icon {
		display: flex;
		overflow: hidden;
		align-items: center;
		justify-content: center;

		width: 4rem;
		min-width: 4rem;
		height: 4rem;
		margin-right: 1rem;

		border: .1rem solid color(dark-10);
		border-radius: 50%;
		background-color: color(blue-bg);

		@include above($mid-desktop) {
			position: absolute;
			top: 50%;
			left: 0;

			margin-right: 0;

			transform: translateY(-50%);
		}

		.icon {
			opacity: .4;
		}

		img,
		svg {
			width: auto;
			max-width: 100%;
		}
	}

	&__desc {
		@include indent-level(margin-bottom, yellow);
	}

	&__topic {
		@include indent-level(margin-bottom, orange);
	}

	&__icon--bg-white {
		background-color: color(white-main);
	}

	&--no-border {
		border-color: transparent;
	}

	&--slide {
		height: 100%;
	}

	&--page {
		padding: 0;

		@include above($mid-desktop) {
			padding: 1rem 0 0;
		}
	}

	&--answer &__top {
		margin-bottom: 2rem;
	}

	&--answer &__body {
		position: relative;

		margin-bottom: 2rem;
		padding-left: 5rem;

		@include above($mid-desktop) {
			margin-bottom: 0;
		}

		&::before {
			position: absolute;
			top: 0;
			bottom: 0;
			left: 2rem;

			width: .1rem;

			content: '';

			background-color: color(dark-10);
		}
	}
}
