@import './config';

.link {
	position: relative;

	display: inline-flex;
	align-items: center;

	transition: color $transition, border-color $transition;
	cursor: pointer;

	color: $link-color;

	&:hover {
		@media (hover: hover) {
			color: $link-hover-color;
		}
	}

	&:hover &__icon {
		* {
			@media (hover: hover) {
				fill: $link-hover-color;
			}
		}
	}

	&:hover &__burger {
		.burger__line {
			@media (hover: hover) {
				background-color: $link-hover-color;
			}
		}
	}

	&__icon {
		flex-shrink: 0;

		&:not(:only-child) {
			margin-left: .8rem;
		}

		* {
			transition: fill #{$transition};
			fill: $link-icon-color;
		}
	}

	&__icon--left {
		&:not(:only-child) {
			margin-left: 0;
			margin-right: .8rem;
		}
	}

	&__burger {
		&:not(:only-child) {
			margin-right: .8rem;
		}

		.burger__line {
			background-color: $link-icon-color;
		}
	}

	&__counter {
		position: absolute;
		top: -.3rem;
		left: 50%;

		display: flex;
		align-items: center;
		justify-content: center;

		height: 1.5rem;
		min-width: 1.5rem;
		padding: 0 .4rem;

		color: color('white-main');
		border-radius: 2rem;
		background-color: color('red-main');

		@include text-base-xsmall();
	}

	&.is-disabled {
		pointer-events: none;
	}

	&--no-link {
		cursor: default;
	}

	&--underline {
		border-bottom: .1rem solid transparent;

		&:hover {
			@media (hover: hover) {
				border-color: $link-hover-color;
			}
		}
	}

	&--weight--bold {
		font-weight: $bold-weight;
	}

	&--color--dark-main {
		color: color(dark-main);
	}

	&--color--dark-main &__icon {
		* {
			fill: color(dark-main);
		}
	}

	&--color--dark-main &__burger {
		.burger__line {
			background-color: color(dark-main);
		}
	}

	&--color--dark-55 {
		color: color(dark-55);

		&:hover {
			@media (hover: hover) {
				color: color(dark-main);
			}
		}
	}

	&--color--dark-55 &__icon {
		* {
			fill: color(dark-55);
		}
	}

	&--color--dark-55 &__burger {
		.burger__line {
			background-color: color(dark-55);
		}
	}

	&--color--dark-55:hover &__icon {
		* {
			@media (hover: hover) {
				fill: color(dark-main);
			}
		}
	}

	&--color--white-main {
		color: color(white-main);

		&:hover {
			@media (hover: hover) {
				color: color(dark-10);
			}
		}
	}

	&--color--white-main &__icon {
		* {
			fill: color(white-main);
		}
	}

	&--color--white-main &__burger {
		.burger__line {
			background-color: color(white-main);
		}
	}

	&--color--white-main:hover &__icon {
		* {
			@media (hover: hover) {
				fill: color(dark-10);
			}
		}
	}

	&--color--white-main:hover &__burger {
		.burger__line {
			@media (hover: hover) {
				background-color: color(dark-10);
			}
		}
	}

	&--color--dark-25 {
		color: color(dark-25);

		&:hover {
			@media (hover: hover) {
				color: color(dark-main);
			}
		}
	}

	&--color--dark-25 &__icon {
		* {
			fill: color(dark-25);
		}
	}

	&--color--dark-40 {
		color: color(dark-40);

		&:hover {
			@media (hover: hover) {
				color: color(dark-main);
			}
		}
	}

	&--color--dark-40 &__icon {
		* {
			fill: color(dark-40);
		}
	}

	&--color--dark-70 {
		color: color(dark-70);

		&:hover {
			@media (hover: hover) {
				color: color(dark-main);
			}
		}
	}

	&--color--dark-70 &__icon {
		* {
			fill: color(dark-70);
		}
	}

	&--color--dark-70 &__burger {
		.burger__line {
			background-color: color(dark-70);
		}
	}

	&--color--dark-70:hover &__icon {
		* {
			@media (hover: hover) {
				fill: color(dark-main);
			}
		}
	}

	&--color--dark-70:hover &__burger {
		.burger__line {
			@media (hover: hover) {
				background-color: color(dark-main);
			}
		}
	}

	&--display--inline {
		display: inline;
	}

	&--display--block {
		display: block;
	}

	&--center {
		display: inline-flex;
		justify-content: center;

		width: 100%;
	}

	&--icon--180deg {
		.icon {
			transform: rotate(180deg);
		}
	}

	&--only-icon {
		.icon {
			margin: 0;
		}
	}

	&--animation &__icon {
		transition: transform #{$transition};
	}

	&--animation:hover &__icon {
		@media (hover: hover) {
			transform: translateX(5px);
			stroke: $link-hover-color;
		}
	}

	&--animation--bounce {
		&::before {
			position: absolute;
			top: calc(50% - 1.5rem);
			left: calc(50% - 1.5rem);

			display: block;

			width: 3rem;
			height: 3rem;

			content: '';
			transform-origin: center;
			animation: 5s ease-in-out infinite bounce-circle-1;

			opacity: 0;
			border: .05rem solid color(dark-40);
			border-radius: 50%;

			@keyframes bounce-circle-1 {
				0% {
					transform: scale(1);

					opacity: 0;
				}

				5% {
					opacity: 1;
				}

				30% {
					transform: scale(2);

					opacity: 0;
				}
			}
		}

		&::after {
			position: absolute;
			top: calc(50% - 1.5rem);
			left: calc(50% - 1.5rem);

			display: block;

			width: 3rem;
			height: 3rem;

			content: '';
			transform-origin: center;
			animation: 5s ease-in-out infinite bounce-circle-2;

			opacity: 0;
			border: .05rem solid color(dark-40);
			border-radius: 50%;

			@keyframes bounce-circle-2 {
				10% {
					transform: scale(1);

					opacity: 0;
				}

				15% {
					opacity: 1;
				}

				40% {
					transform: scale(2);

					opacity: 0;
				}
			}
		}
	}
	$this: &;
}
