// кастомизация сеток https://getbootstrap.com/docs/5.0/layout/grid/#customizing-the-grid
// переменные нужно переопределять до импорта

$grid-breakpoints: (
	xxs: 0,
	xs: $phone-landscape,
	sm: $tablet,
	md: $desktop,
	lg: $mid-desktop,
	xl: $large-desktop,
	xxl: $big-desktop,
);

