.slider-dots {
	display: flex;
	justify-content: center;

	width: 100%;

	@include margin-x(-.3rem);

	&__item {
		width: 1.4rem;
		min-width: 1.4rem;
		height: 1.4rem;

		cursor: pointer;
		transition: background-color $transition;

		border-radius: 50%;
		outline: none;
		background-color: color(green-main);

		@include margin-x(.3rem);
	}

	&__item--active {
		background-color: color(dark-main);
	}
}
