@import './config';

.tippy-box[data-theme~='custom'] {
	background-color: $tooltip-bg-color;
	box-shadow: 0 .3rem 3rem rgba(color(dark-main), .1);
	border-radius: 0;

	&[data-placement^='top'] {
		& > .tippy-arrow {
			&::before {
				border-top-color: transparent;
			}
		}
	}

	&[data-placement^='bottom'] {
		& > .tippy-arrow {
			&::before {
				border-bottom-color: transparent;
			}
		}
	}

	&[data-placement^='left'] {
		& > .tippy-arrow {
			&::before {
				border-left-color: transparent;
			}
		}
	}

	&[data-placement^='right'] {
		& > .tippy-arrow {
			&::before {
				border-right-color: transparent;
			}
		}
	}

	.tippy-content {
		padding: 0;
	}
}

.tooltip {
	min-width: 22rem;

	&__body {
		padding: 1.5rem 2rem;
	}

	&__footer {
		padding: 1.5rem 2rem;

		background-color: $tooltip-footer-bg-color;
	}
}

.tooltip-icon {
	font-size: 1.4rem;
	line-height: 1;

	cursor: pointer;

	border-radius: 100%;
	background-color: $tooltip-icon-bg-color;

	@include size(1.8rem);
	@include center-flex;

	&--blue {
		display: inline-flex;

		color: color('blue-main');
		background-color: color('blue-light');
	}

	&--translate--02 {
		transform: translateY(-.2rem);
	}
}
