.breadcrumbs {
	overflow: auto;

	margin-left: -2rem;
	margin-right: -2rem;
	margin-bottom: 1.2rem;

	&__list {
		display: flex;
		align-items: center;
	}

	&__item {
		position: relative;

		display: inline-flex;
		align-items: center;
		flex-shrink: 0;

		&::after {
			display: block;

			width: .8rem;
			height: .1rem;
			margin-right: .7rem;
			margin-left: .7rem;

			content: '';

			background-color: color(dark-55);
		}

		&:first-of-type {
			padding-left: 2rem;
		}

		&:last-of-type {
			padding-right: 2rem;

			&::after {
				display: none;
			}
		}
	}

	&__link {
		white-space: nowrap;
	}
}
