.event-date {
	&__list {
		@include above($tablet) {
			display: flex;
			align-items: center;
		}
	}

	&__item {
		margin-bottom: 2rem;

		@include above($tablet) {
			margin-bottom: 0;
			margin-right: 4rem;
		}

		&:last-of-type {
			margin-bottom: 0;

			@include above($tablet) {
				margin-right: 0;
			}
		}
	}
}
