.section {
	&:not(:first-child) {
		@include indent-level(margin-top, purple);
	}

	&:not(:last-child) {
		@include indent-level(margin-bottom, purple);
	}

	&__container {
		@include above($mid-desktop) {
			@include padding-x(9rem);
		}

		@include above($large-desktop) {
			@include padding-x(10rem);
		}

		@include above($big-desktop) {
			@include padding-x(14rem);
		}
	}

	&__wrapper {
		width: 100%;
		margin-right: auto;
		margin-left: auto;

		@include padding-x(2rem);

		@include above($desktop) {
			@include padding-x(6rem);
		}

		@include above($mid-desktop) {
			@include padding-x(4rem);
		}

		@include above($large-desktop) {
			max-width: 100%;

			@include padding-x(5rem);
		}
	}

	&__wrapper--no--pr {
		padding-right: 0!important;
	}

	&__row {
		display: flex;
		flex-wrap: wrap;

		margin: 0 -.8rem;
	}

	&__col {
		width: 100%;
		padding: 0 .8rem;

		@include above($tablet-landscape) {
			flex: 0 0 50%;

			max-width: 50%;
		}

		&:not(:last-child) {
			margin-bottom: 1.6rem;

			@include above($tablet-landscape) {
				margin-bottom: 0;
			}
		}
	}

	&__title {
		@include indent-level(margin-bottom, blue);
	}

	&__title--flex {
		@include above($tablet) {
			display: flex;
			align-items: center;
			justify-content: space-between;
		}
	}

	&__description {
		@include indent-level(margin-bottom, orange);
	}

	&__main {
		@include indent-level(margin-bottom, blue);
	}

	.section__main {
		@include above($tablet) {
			margin-bottom: 0;
		}
	}

	&--no--mb {
		margin-bottom: 0 !important;
	}

	&--no--mt {
		margin-top: 0 !important;
	}

	&--no--pb {
		padding-bottom: 0 !important;
	}

	&--no--pt {
		padding-top: 0 !important;
	}

	&--inner {
		@include indent-level(padding-top, purple);
		@include indent-level(padding-bottom, purple);
	}

	&--border {
		@include above($desktop) {
			position: relative;
		}

		&::before {
			@include above($desktop) {
				position: absolute;
				z-index: 2;
				top: 6.8rem;
				bottom: 0;
				left: 2rem;

				width: .2rem;

				content: '';

				background-color: color(green-main);
			}

			@include above($mid-desktop) {
				top: 9rem;
				left: 4rem;

				transform: translateX(-.1rem);
			}

			@include above($large-desktop) {
				left: 5rem;
			}
		}
	}

	&--panel {
		position: fixed;
		z-index: 100;
		right: 0;
		bottom: 0;
		left: 0;

		background-color: color(white-main);
		box-shadow: 0 -0.4rem 0.8rem rgba(7, 21, 10, .1);
	}

	&--not-in-visible {
		transition: opacity $transition;

		opacity: 1;

		&.is-visible {
			pointer-events: none;

			opacity: 0;
		}
	}

	&--bg {
		position: relative;
	}

	&--mb {
		@include indent-level(margin-bottom, purple);
	}

	&:last-child:not(.section--inner):not(.footer) {
		@include indent-level(margin-bottom, purple);
	}

}

.row {
	&--gap--6 {
		@include indent-level(margin-right, 3, 'x', true);
		@include indent-level(margin-left, 3, 'x', true);

		& > .col,
		& > [class*='col-'] {
			@include indent-level(padding-right, 3, 'x');
			@include indent-level(padding-left, 3, 'x');
		}
	}

	&--gap--10 {
		@include indent-level(margin-right, 5, 'x', true);
		@include indent-level(margin-left, 5, 'x', true);

		& > .col,
		& > [class*='col-'] {
			@include indent-level(padding-right, 5, 'x');
			@include indent-level(padding-left, 5, 'x');
		}
	}

	&--gap--20 {
		@include indent-level(margin-right, 10, 'x', true);
		@include indent-level(margin-left, 10, 'x', true);

		& > .col,
		& > [class*='col-'] {
			@include indent-level(padding-right, 10, 'x');
			@include indent-level(padding-left, 10, 'x');
		}
	}

	&--gap--30 {
		@include indent-level(margin-right, 15, 'x', true);
		@include indent-level(margin-left, 15, 'x', true);

		& > .col,
		& > [class*='col-'] {
			@include indent-level(padding-right, 15, 'x');
			@include indent-level(padding-left, 15, 'x');
		}
	}

	&--gap--40 {
		@include indent-level(margin-right, 20, 'x', true);
		@include indent-level(margin-left, 20, 'x', true);

		& > .col,
		& > [class*='col-'] {
			@include indent-level(padding-right, 20, 'x');
			@include indent-level(padding-left, 20, 'x');
		}
	}

	&--gap--60 {
		@include indent-level(margin-right, 30, 'x', true);
		@include indent-level(margin-left, 30, 'x', true);

		& > .col,
		& > [class*='col-'] {
			@include indent-level(padding-right, 30, 'x');
			@include indent-level(padding-left, 30, 'x');
		}
	}

	&--gap--30--vertical {
		@include indent-level(margin-top, 15, 'x', true);
		@include indent-level(margin-bottom, 15, 'x', true);

		& > .col,
		& > [class*='col-'] {
			@include indent-level(padding-top, 15, 'x');
			@include indent-level(padding-bottom, 15, 'x');
		}
	}
}


.section-navigation {
	position: relative;

	&__nav {
		position: absolute;
		height: 100%;
		left: 0;
		top: 0;
	}
}
