@import './config';

.paginator {
	$this: &;
	display: flex;
	align-items: center;
	flex-direction: column;

	width: 100%;

	@include above($tablet) {
		align-items: flex-start;
	}

	&__wrapper {
		display: flex;
		align-items: center;
		flex-direction: column;

		@include above($tablet) {
			flex-direction: row;
		}
	}

	&__list {
		display: flex;
		align-items: center;

		list-style: none;

		&--center {
			flex-wrap: wrap;
			justify-content: center;
		}
	}

	&__item {
		@include padding-x(.1rem);

		&.disabled {
			pointer-events: none;
		}
	}

	&__item.is-active &__link {
		border-color: color(green-main);
		background: transparent;
	}

	&__item--prev,
	&__item--next {
		#{$this}__link {
			width: 3rem;

			background-color: $paginator-item-controls-bg-color;

			&:hover {
				@media (hover: hover) {
					border-color: transparent;
					background-color: transparent;
				}
			}
		}
	}

	&__link {
		transition: background-color #{$transition}, border-color #{$transition};

		border: 1px solid transparent;

		@include size(4rem);
		@include center-flex;

		&:hover {
			@media (hover: hover) {
				border-color: color(blue-sky);
				background-color: color(blue-sky);
			}
		}
	}

	&__link:hover .link__text {
		@media (hover: hover) {
			color: color(dark-main);
		}
	}

	&__icon {
		transition: fill #{$transition};
	}

	&__link:hover &__icon {
		@media (hover: hover) {
			fill: $paginator-item-bg-color;
		}
	}

	&__icon--prev {
		transform: rotate(180deg);
	}

	&__button {
		//@include indent-level(margin-bottom, orange);
		//s@include center-flex;
	}

	&__controls {
		display: block;

		margin-bottom: 3rem;

		@include above($tablet) {
			margin-right: 2rem;
			margin-bottom: 0;
		}
	}

}
