@import './config';

$this: &;

.button {
	$this: &;

	font-weight: $button-font-weight;

	display: inline-flex;
	align-items: center;
	justify-content: center;

	min-width: $button-min-width;
	height: $button-height;
	padding: $button-padding;

	cursor: $button-cursor-style;
	transition: $button-transition;

	color: $button-color;
	border: $button-border transparent;

	background-color: $button-bg-color;

	@include text-base-medium;

	&:hover {
		@media (hover: hover) {
			color: $button-hover-color;
			background-color: $button-hover-bg-color;
		}
	}

	&:active {
		@media (hover: hover) {
			transition-delay: .2s;

			color: $button-active-color;

			background-color: $button-active-bg-color;
		}
	}

	&__wrapper {
		position: relative;

		display: flex;
		align-items: center;
		justify-content: center;
	}

	&__text {
		position: relative;

		display: block;

		overflow: hidden;

		white-space: nowrap;
		text-overflow: ellipsis;
	}

	&__icon,
	&__icon.icon {
		flex-shrink: 0;

		fill: $button-color;

		&:not(:only-child) {
			margin-left: $button-icon-margin;
		}
	}

	&__counter {
		font-weight: 700;

		display: flex;
		align-items: center;
		justify-content: center;

		min-width: 2.2rem;
		height: 2.2rem;
		padding: 0 .6rem;

		color: color(white-main);
		border-radius: 2rem;
		background: color(green-main);

		@include text-base-medium();

		&:not(:only-child) {
			margin-left: $button-icon-margin;
		}
	}

	&__burger {
		&:not(:only-child) {
			margin-right: $button-icon-margin;
		}

		.burger__line {
			background: $button-color;
		}
	}

	&__icon--left,
	&__icon--left.icon {
		margin-right: $button-icon-margin;

		&:not(:only-child) {
			margin-left: 0;
		}
	}

	&--not-clickable {
		pointer-events: none;
	}

	&--size--small {
		height: $button-small-height;
		padding: $button-small-padding;

		@include text-base-small;
	}

	&--size--large {
		height: $button-big-height;
		padding: $button-big-padding;
	}

	&--t-base--size-medium {
		@include text-base-medium;
	}

	&--type--bordered {
		color: $button-bg-color;
		border: $button-border $button-bg-color;
		background-color: transparent;

		&:hover {
			@media (hover: hover) {
				color: $button-hover-bg-color;
				border: $button-border $button-hover-bg-color;
				background-color: transparent;
			}
		}

		&:active {
			@media (hover: hover) {
				transition-delay: $button-active-transition-delay;

				color: $button-active-bg-color;
				border: $button-border $button-active-bg-color;

				background-color: transparent;
			}
		}
	}

	&--type--quick-view {
		color: $button-quick-view-color;
		border: $button-border $button-quick-view-border-color;
		background-color: $button-quick-view-bg-color;

		&:hover {
			@media (hover: hover) {
				color: $button-quick-view-color;
				border: $button-border $button-quick-view-border-color;
				background-color: $button-quick-view-bg-color;
			}
		}

		&:active {
			@media (hover: hover) {
				color: $button-quick-view-color;
				border: $button-border $button-quick-view-border-color;
				background-color: $button-quick-view-bg-color;
			}
		}

		#{$this}__icon,
		#{$this}__icon.icon {
			fill: $button-quick-view-color;
		}
	}

	&--theme--accent {
		color: $button-accent-color;
		background-color: $button-accent-bg-color;

		&:hover {
			@media (hover: hover) {
				color: $button-accent-hover-color;
				background-color: $button-accent-hover-bg-color;
			}
		}

		&:active {
			@media (hover: hover) {
				transition-delay: .2s;

				color: $button-accent-active-color;

				background-color: $button-accent-active-bg-color;
			}
		}

		#{$this}__icon,
		#{$this}__icon.icon {
			fill: $button-accent-color;
		}

		&#{$this}--type--bordered {
			color: $button-accent-bg-color;
			border: $button-border $button-accent-bg-color;
			background-color: transparent;

			&:hover {
				@media (hover: hover) {
					color: $button-accent-hover-bg-color;
					border: $button-border $button-accent-hover-bg-color;
					background-color: transparent;
				}
			}

			&:active {
				@media (hover: hover) {
					transition-delay: $button-active-transition-delay;
					background-color: transparent;
					color: $button-accent-active-bg-color;
					border: $button-border $button-accent-active-bg-color;
				}
			}
		}
		&#{$this}--type--bordered-gray {
			border-color: $button-gray-bd-color;
	
			&:hover {
				@media (hover: hover) {
					border-color: $button-gray-bd-hover-color;
				}
			}
		}
	}

	&--theme--orange {
		background-color: color(yellow-main);

		&:hover {
			@media (hover: hover) {
				background-color: color(yellow-hover);
			}
		}

		&:active {
			@media (hover: hover) {
				background-color: color(yellow-light);
			}
		}
	}

	&--theme--white {
		color: $button-white-theme-color;
		background-color: $button-white-bg-color;

		&:hover {
			@media (hover: hover) {
				color: $button-white-theme-color;
				background-color: $button-white-bg-color;
			}
		}
	}

	&--theme--blue-bg {
		color: $button-blue-color;
		background-color: $button-blue-bg-color;

		&:hover {
			@media (hover: hover) {
				color: $button-blue-color;
				background-color: $button-hover-bg-color;
			}
		}
	}

	&--theme--blue-sky-bg {
		color: $button-sky-color;
		background-color: $button-sky-bg-color;

		&:hover {
			@media (hover: hover) {
				color: $button-sky-color;
				background-color: $button-sky-hover-bg-color;
			}
		}
	}

	&--type--bordered-gray {
		border-color: $button-gray-bd-color;

		&:hover {
			@media (hover: hover) {
				border-color: $button-gray-bd-hover-color;
			}
		}
	}

	&--weight-normal {
		font-weight: 400;
	}

	&.is-active {
		color: color(white-main);
		border-color: color(green-main);
		background-color: color(green-main);

		&:hover {
			color: color(white-main);
			border-color: color(green-main);
			background-color: color(green-main);
		}
	}

	&--theme--disabled,
	&--disabled,
	&.is-disabled,
	&[disabled] {
		pointer-events: none;

		color: $button-disabled-color;

		background-color: $button-disabled-bg-color;

		&:hover {
			@media (hover: hover) {
				color: $button-disabled-color;
				background-color: $button-disabled-bg-color;
			}
		}

		&:active {
			@media (hover: hover) {
				color: $button-disabled-color;
				background-color: $button-disabled-bg-color;
			}
		}

		#{$this}__icon,
		#{$this}__icon.icon {
			fill: $button-disabled-color;
		}
	}

	&--width--full {
		width: 100%;
	}
}
