@import './config';

.count-control {
	position: relative;

	display: flex;
	align-items: center;

	width: 100%;

	transition: background-color #{$transition};

	background-color: $count-control-bg-color;

	&:hover &__input {
		border-color: $input-hover-border-color;
	}

	&__input {
		width: 12rem;
		height: 4rem;
		padding-left: 3rem;
		padding-right: 3rem;

		transition: color #{$transition};

		text-align: center;

		color: $count-control-input-color;

		@include text-base-large;

		.is-active & {
			color: $count-control-input-active-color;
		}
	}

	&__btn {
		position: absolute;
		z-index: 2;
		top: 0;

		width: 3rem;

		height: 100%;

		cursor: pointer;

		&::before,
		&::after {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;

			width: 1rem;
			height: .16rem;
			margin: auto;

			content: '';
			transition: background-color #{$transition};

			border-radius: 3rem;

			background-color: $count-control-btn-bg-color;
		}

		&:hover {
			&::before,
			&::after {
				@media (hover: hover) {
					background-color: $count-control-btn-hover-bg-color;
				}
			}
		}

		.is-active &::before,
		.is-active &::after {
			background-color: $count-control-btn-active-bg-color;
		}

		.is-active &:hover {
			&::before,
			&::after {
				@media (hover: hover) {
					background-color: $count-control-btn-active-hover-bg-color;
				}
			}
		}

		&.is-disabled {
			pointer-events: none;

			opacity: .5;
		}
	}

	&__btn--inc {
		right: 0;

		&::after {
			transform: rotate(90deg);
		}
	}

	&__info,
	&__cart-info {
		position: absolute;
		left: 0;

		visibility: hidden;

		width: 100%;

		transition: opacity #{$transition}, visibility #{$transition};
		text-align: center;
		white-space: nowrap;

		opacity: 0;

		@include text-base-small;
	}

	&__info {
		bottom: 100%;

		color: $count-control-info-color;

		&.is-active {
			visibility: visible;

			opacity: 1;
		}
	}

	.is-active & {
		background-color: $count-control-active-bg-color;
	}
}
