.label-item {
	display: inline-flex;

	padding: .2rem .9rem;

	border-radius: 1.2rem 0;

	&__text {
		color: color(white-main);
	}

	&--bg--yellow-main {
		background-color: color(yellow-main);
	}

	&--bg--gray {
		background-color: color(dark-bg);
	}

	&--bg--yellow-light {
		background-color: color(yellow-bg);
	}

	&--text--green &__text {
		color: color(green-main);
	}

	&--text--gray &__text {
		color: color(dark-55);
	}
}
