// ----------COLORS----------
// base-theme
$button-bg-color: $color-base;
$button-hover-bg-color: $color-base-hover;
$button-active-bg-color: $color-base-active;

$button-color: color(white-main);
$button-hover-color: color(white-main);
$button-active-color: color(white-main);

// accented-theme
$button-accent-bg-color: $color-accent;
$button-accent-hover-bg-color: $color-accent-hover;
$button-accent-active-bg-color: $color-accent-active;

$button-accent-color: color(white-main);
$button-accent-hover-color: color(white-main);
$button-accent-active-color: color(white-main);

// white-theme
$button-white-bg-color: color(white-main);
$button-white-theme-color: color(dark-main);

// white-theme
$button-blue-bg-color: color(blue-bg);
$button-blue-hover-bg-color: color(white-main);
$button-blue-color: color(dark-main);

// sky-theme
$button-sky-bg-color: color(blue-sky);
$button-sky-hover-bg-color: color(blue-light);
$button-sky-color: color(dark-main);

// button--type--bordered-gray
$button-gray-bd-color: color(dark-10);
$button-gray-bd-hover-color: color(dark-main);

// disabled-theme
$button-disabled-bg-color: color(dark-bg);
$button-disabled-color: color(dark-25);

// quick view
$button-quick-view-bg-color: color(white-main);
$button-quick-view-color: color(gray-main);
$button-quick-view-border-color: color(gray-middle);

// ----------FONT----------
$button-font-weight: $bold-weight;

// ----------SIZE----------
$button-height: 4rem;
$button-big-height: 5rem;
$button-small-height: 3.4rem;
$button-padding: 0 2rem;
$button-big-padding: 0 2.5rem;
$button-small-padding: 0 1.4rem;
$button-min-width: auto;

// ----------MISC----------
$button-border: .1rem solid;
$button-cursor-style: pointer;
$button-transition: background-color #{$transition}, border-color #{$transition};
$button-active-transition-delay: .2s;
$button-icon-margin: 1.2rem;
