.checkbox-button {
    display: inline-flex;
    &.is-disabled {
		pointer-events: none;
	}
    input {
		@include hidden;
        &:checked {
			& ~ .button {
                background-color: color(green-main);
                border-color: color(green-main);
                color: color(white-main);
            
                &:hover {
                    background-color: color(green-main);
                    border-color: color(green-main);
                    color: color(white-main);
                }
            }
        }
    }

}