.steps {
	@include above($tablet) {
		padding-left: 7rem;
		padding-right: 7rem;
	}

	@include above($desktop) {
		padding-left: 0;
		padding-right: 0;
	}

	@include above($desktop) {
		position: relative;

		padding-right: 1rem;
		padding-left: 1rem;
	}

	@include above($mid-desktop) {
		padding-left: 4rem;
		padding-right: 4rem;
	}

	@include above($big-desktop) {
		padding-left: 10rem;
		padding-right: 10rem;
	}

	&::before {
		@include above($desktop) {
			position: absolute;
			top: 2.5rem;
			right: 0;
			left: 0;

			height: .2rem;

			content: '';

			background-color: color(green-main);
		}

		@include above($big-desktop) {
			left: 6rem;
			right: 6rem;
		}
	}

	&__list {
		margin-bottom: -4rem;

		@include above($tablet) {
			display: flex;
			flex-wrap: wrap;
			justify-content: center;

			margin-left: -2rem;
			margin-right: -2rem;
		}

		@include above($desktop) {
			margin-left: -1rem;
			margin-right: -1rem;
		}

		@include above($mid-desktop) {
			margin-left: -2rem;
			margin-right: -2rem;
		}

		@include above($large-desktop) {
			margin-left: -3rem;
			margin-right: -3rem;
		}

		@include above($big-desktop) {
			margin-left: -6rem;
			margin-right: -6rem;
		}
	}

	&__col {
		margin-bottom: 4rem;

		@include above($tablet) {
			width: auto;
			max-width: 33.3333%;
			padding-left: 2rem;
			padding-right: 2rem;
		}

		@include above($desktop) {
			max-width: 20%;
		}

		@include above($desktop) {
			padding-left: 1rem;
			padding-right: 1rem;
		}

		@include above($mid-desktop) {
			padding-left: 2rem;
			padding-right: 2rem;
		}

		@include above($large-desktop) {
			padding-left: 3rem;
			padding-right: 3rem;
		}

		@include above($big-desktop) {
			padding-left: 6rem;
			padding-right: 6rem;
		}
	}

	&__col:first-of-type &__item-number {
		&::after {
			height: 4rem;

			@include above($tablet) {
				height: 1px;
			}
		}
	}

	&__col:nth-of-type(4) &__item-number,
	&__col:nth-of-type(5) &__item-number {
		&::after {
			@include above($tablet) {
				left: auto;
				right: -7rem;
			}
		}
	}

	&__item {
		display: flex;
		align-items: center;

		@include above($tablet) {
			display: block;
		}
	}

	&__item-number {
		font-size: 3rem;
		line-height: 3rem;

		position: relative;

		flex-shrink: 0;

		margin-right: 1rem;
		padding-left: .8rem;

		color: color(green-main);

		@include above($tablet) {
			margin-right: 0;
			margin-bottom: 1.5rem;
		}

		&::before {
			position: absolute;
			bottom: .4rem;
			left: 0;

			width: .6rem;
			height: .6rem;

			content: '';

			-webkit-border-radius: 50%;
			-moz-border-radius: 50%;
			border-radius: 50%;
			background-color: color(green-main);
		}

		&::after {
			position: absolute;
			bottom: .4rem;
			left: .2rem;

			width: .2rem;
			height: 11rem;

			content: '';

			background-color: color(green-main);

			@include above($tablet) {
				left: -2rem;

				width: 1100px;
				height: 1px;
			}

			@include above($desktop) {
				display: none;
			}
		}
	}

	&--full {
		@include indent-level(padding-bottom, blue);

		@include above($tablet) {
			padding-left: 3rem;
			padding-right: 3rem;
		}

		@include above($desktop) {
			padding-left: 3rem;
			padding-right: 3rem;
		}

		@include above($mid-desktop) {
			padding-left: 3rem;
			padding-right: 3rem;
		}

		@include above($big-desktop) {
			padding-left: 3rem;
			padding-right: 3rem;
		}

		&::before {
			@include above($big-desktop) {
				left: 0;
				right: 0;
			}
		}
	}

	&--full &__list {
		position: relative;

		@include above($tablet) {
			justify-content: flex-start;
		}

		@include above($desktop) {
			margin-left: -4rem;
			margin-right: -4rem;
		}

		&::before {
			position: absolute;
			top: -1rem;
			bottom: -1rem;
			left: .1rem;

			width: .2rem;

			content: '';

			background-color: color(green-main);

			@include above($tablet) {
				display: none;
			}
		}
	}

	&--full &__col {
		@include above($tablet) {
			width: 50%;
			max-width: none;
		}

		@include above($desktop) {
			width: 33.3333%;
			padding-left: 4rem;
			padding-right: 4rem;
		}
	}

	&--full &__col:first-of-type &__item-number::after {
		height: .2rem;
	}

	&--full &__item {
		align-items: flex-start;
	}

	&--full &__col:nth-of-type(3) &__item-number::after {
		@include above($tablet) {
			left: -6rem;
			right: 0;
		}
	}

	&--full &__item-number {
		&::after {
			display: none;

			@include above($tablet) {
				right: -10rem;
				left: -2rem;

				display: block;

				width: auto;
				height: .2rem;
			}

			@include above($desktop) {
				display: none;
			}
		}
	}

	&--type-2 {
		@include above($big-desktop) {
			padding-left: 4rem;
			padding-right: 4rem;
		}

		&:before {
			@include above($big-desktop) {
				left: 0;
				right: 0;
			}
		}
	}
}
