.animation-pattern-lines {
	$this: &;

	position: relative;

	&__svg {
		position: absolute;
		z-index: -1;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;

		display: flex;
		overflow: hidden;
		flex-direction: column;

		background: color(blue-bg) url('../img/animation-pattern-lines/cubes.svg') repeat top left;
	}

	&__svg-row {
		display: grid;
    	grid-template-columns: 432px 432px 432px 432px 432px;
	}

	&__svg-row--1 {
		order: 0;
	}

	&__svg-row--2 {
		order: 4;

		transform: translateY(45px);
	}

	&__svg-row--3 {
		order: 3;

		transform: translateY(30px);
	}

	&__svg-row--4 {
		order: 2;

		transform: translateY(15px);
	}

	&__svg-col:nth-child(6), &__svg-col:nth-child(7), &__svg-col:nth-child(8), &__svg-col:nth-child(9), &__svg-col:nth-child(10) {
		margin-top: 15px;
	}

	&__svg-col:nth-child(11), &__svg-col:nth-child(12), &__svg-col:nth-child(13), &__svg-col:nth-child(14), &__svg-col:nth-child(15) {
		margin-top: 40px;
	}

	&__svg-col:nth-child(16), &__svg-col:nth-child(17), &__svg-col:nth-child(18), &__svg-col:nth-child(19), &__svg-col:nth-child(20) {
		margin-top: 15px;
	}

	&__svg-col {
		display: block;
		flex-shrink: 0;

		transform-origin: center;
	}

	&__svg-col--x {
		transform: rotateX(180deg);
	}

	&__svg-col--y {
		transform: rotateY(180deg);
	}

	[data-stroke] path {
		transition: all .5s ease;

		opacity: 0;
	}

	&__line--type--1 {
		transform: translate(17px, -3px);

		&#{$this}__svg-col--x {
			transform: translate(17px, -3px) rotateX(180deg);
		}

		&#{$this}__svg-col--y {
			transform: translate(17px, -3px) rotateY(180deg);
		}
	}

	&__line--type--2 {
		transform: translate(27px, -1px);

		&#{$this}__svg-col--x {
			transform: translate(27px, -1px) rotateX(180deg);
		}

		&#{$this}__svg-col--y {
			transform: translate(27px, -1px) rotateY(180deg);
		}
	}

	&__line--type--3 {
		transform: translate(36px, 8px);

		&#{$this}__svg-col--x {
			transform: translate(36px, 8px) rotateX(180deg);
		}

		&#{$this}__svg-col--y {
			transform: translate(36px, 8px) rotateY(180deg);
		}
	}

	&__line--type--4 {
		transform: translate(28px, 8px);

		&#{$this}__svg-col--x {
			transform: translate(28px, 8px) rotateX(180deg);
		}

		&#{$this}__svg-col--y {
			transform: translate(28px, 8px) rotateY(180deg);
		}
	}

	&__line--type--5 {
		transform: translate(57px, 7px);

		&#{$this}__svg-col--x {
			transform: translate(57px, 7px) rotateX(180deg);
		}

		&#{$this}__svg-col--y {
			transform: translate(57px, 7px) rotateY(180deg);
		}
	}

	&--type--2 &__svg{
		align-items: flex-end;
		background: url('../img/animation-pattern-lines/pattern-bg-small.svg') no-repeat 100% 16px;
	}

	&--type--2 &__svg-row{
		display: grid;
		grid-template-columns: 1fr 1fr;
		grid-template-rows: 1fr 1fr;
		gap: 0px 0px;
		grid-template-areas:
			". ."
			"pattern pattern";
	}

	&--type--2 &__line {
		justify-self: end;
	}

	&--type--2 &__line--type--3 {
		grid-area: pattern;
	}


	&--type--2 &__line--type--1 {
		transform: translate(-48px, -2px);

		&#{$this}__svg-col--x {
			transform: translate(-48px, -2px) rotateX(180deg);
		}

		&#{$this}__svg-col--y {
			transform: translate(-48px, -2px) rotateY(180deg);
		}
	}

	&--type--2 &__line--type--3 {
		transform: translate(165px, 14px);

		&#{$this}__svg-col--x {
			transform: translate(165px, 14px) rotateX(180deg);
		}

		&#{$this}__svg-col--y {
			transform: translate(165px, 14px) rotateY(180deg);
		}
	}

	&--type--3 &__svg{
		align-items: center;
		background: url('../img/animation-pattern-lines/pattern-bg-big.svg') no-repeat center top;
	}


	&--type--3 &__line--type--1 {
		transform: translate(150px, 8px);

		&#{$this}__svg-col--x {
			transform: translate(150px, 8px) rotateX(180deg);
		}

		&#{$this}__svg-col--y {
			transform: translate(150px, 8px) rotateY(180deg);
		}
	}

	&--type--3 &__line--type--2 {
		transform: translate(19px, -3px);

		&#{$this}__svg-col--x {
			transform: translate(19px, -3px) rotateX(180deg);
		}

		&#{$this}__svg-col--y {
			transform: translate(19px, -3px) rotateY(180deg);
		}
	}

	&--type--3 &__line--type--3 {
		transform: translate(30px, -3px);

		&#{$this}__svg-col--x {
			transform: translate(30px, -3px) rotateX(180deg);
		}

		&#{$this}__svg-col--y {
			transform: translate(30px, -3px) rotateY(180deg);
		}
	}

	&--type--3 &__line--type--4 {
		transform: translate(60px, 8px);

		&#{$this}__svg-col--x {
			transform: translate(60px, 8px) rotateX(180deg);
		}

		&#{$this}__svg-col--y {
			transform: translate(60px, 8px) rotateY(180deg);
		}
	}

	&--type--3 &__line--type--5 {
		transform: rotate(120deg) translate(130px, 2px);
	}
}

