@import './config';

.icon {
	display: flex;
	align-items: center;
	justify-content: center;

	width: 1.6rem;
	height: 1.6rem;
	fill: $icon-fill-color;

	&--size--small {
		width: 1.2rem;
		height: 1.2rem;
	}

	&--size--md {
		width: 1.4rem;
		height: 1.4rem;
	}

	&--size--normal {
		width: 2rem;
		height: 2rem;
	}

	&--size--large {
		width: 2.4rem;
		height: 2.4rem;
	}

	&--size--xl {
		width: 3.4rem;
		height: 3.4rem;
	}

	&--size--xxl {
		width: 4rem;
		height: 4rem;
	}

	&--color--accent {
		fill: $color-accent;
	}

	&--color--base {
		fill: $color-base;
	}

	&--color--white {
		fill: #ffffff;
	}

	&--color--dark-55 {
		fill: color(dark-55);
	}

	&--stroke--green-main {
		stroke: $color-base;
	}

	&--stroke--dark {
		stroke: color(dark-70);
	}

	&--rotate--180 {
		transform: rotate(180deg);
	}

	&--rotate--90 {
		transform: rotate(90deg);
	}

	&--color--blue {
		fill: color(blue-main);
	}
}
