.simple-table {
	&__row {
		display: flex;
		align-items: center;
		border-bottom: 1px solid color(dark-10);
		padding: 2rem 1rem;

		&:first-of-type {
			border-top: 1px solid color(dark-10);
		}
	}

	&__col--1 {
		padding-right: 1rem;

		@include above($large-desktop) {
			flex: 0 0 20rem;
		}
	}
}
