.search-form {
	display: flex;
	flex-wrap: wrap;
	overflow: hidden;

	background-color: color('white-main');

	&__wrapper {
		display: flex;
		flex-grow: 1;

		height: 5.1rem;
		padding: 0 2rem;

		border-bottom: .1rem solid color('dark-10');

		@include above($desktop) {
			padding: 0;
		}
	}

	&__input {
		position: relative;

		flex: 1;

		padding: 0 1rem;

		input[type='text'] {
			width: 100%;
			height: 100%;
			margin: 0;
			padding: 0;

			text-overflow: ellipsis;

			color: color('dark-main');
			border: none;
			outline: none;
			background-color: #ffffff;
			box-shadow: none;

			@include text-base-medium();

			&:focus {
				background-color: #ffffff;
			}

			&::placeholder {
				color: color('dark-55');
			}
		}
	}

	&__close {
		padding: 0 2rem;

		border-bottom: .1rem solid color('dark-10');
		border-left: .1rem solid color('dark-10');

		@include above($desktop) {
			display: none;
		}
	}

	&--page &__wrapper {
		padding-right: 1.6rem;
		padding-left: 1.6rem;

		border: 1px solid color(dark-10);
	}
}
