@mixin below($width, $orientation: 'width') {
	@media all and (max-#{$orientation}: $width) {
		@content;
	}
}

@mixin above($width, $orientation: 'width') {
	@media all and (min-#{$orientation}: $width) {
		@content;
	}
}

@mixin between($minWidth, $maxWidth, $orientation: 'width') {
	@media all and (min-#{$orientation}: $minWidth) and (max-#{$orientation}: $maxWidth) {
		@content;
	}
}

@mixin hide-text {
	overflow: hidden;

	white-space: nowrap;
	text-indent: 100%;
}

@mixin hidden {
	position: absolute;

	overflow: hidden;
	clip: rect(0 0 0 0);

	margin: -1px;
	padding: 0;

	border: 0;

	@include size(1px);
}

@mixin size($width, $height: $width) {
	width: $width;
	height: $height;
}

@mixin fadeIn($time: .2s, $delay: 0s) {
	visibility: visible;

	transition: opacity $time ease-in $delay, visibility $delay ease-in $delay;
	pointer-events: all;

	opacity: 1;
}

@mixin fadeOut($time: .2s, $delay: 0s) {
	visibility: hidden;

	transition: opacity $time ease-out $delay, visibility $time + $delay ease-out $time + $delay;
	pointer-events: none;

	opacity: 0;
}

@mixin margin-x($size) {
	margin-left: $size;
	margin-right: $size;
}

@mixin padding-x($size) {
	padding-left: $size;
	padding-right: $size;
}

@mixin margin-y($size) {
	margin-top: $size;
	margin-bottom: $size;
}

@mixin padding-y($size) {
	padding-top: $size;
	padding-bottom: $size;
}

@mixin center($pos: absolute, $vert: true, $horiz: true) {
	position: $pos;

	@if ($horiz) {
		left: 50%;
	}

	@if ($vert) {
		top: 50%;
	}

	@if ($horiz == false) {
		transform: translateY(-50%);
	}

 @else if ($vert == false) {
		transform: translateX(-50%);
	}

 @else {
		transform: translate(-50%, -50%);
	}
}

@mixin slideIn($time: .25s, $delay: 0s, $ease: ease-in) {
	visibility: visible;

	transition: transform $time $ease $delay, opacity $time $ease $delay,
		visibility $delay $ease $delay;
	transform: translateY(0);
	pointer-events: all;

	opacity: 1;
}

@mixin slideOut($time: .25s, $delay: 0s, $ease: ease-out) {
	visibility: hidden;

	transition: transform $time $ease $delay, opacity $time $ease $delay,
		visibility $time + $delay $ease $time + $delay;
	transform: translateY(-15px);
	pointer-events: none;

	opacity: 0;
}

@mixin scaleIn($time: .25s, $delay: 0s) {
	visibility: visible;

	transition: transform $time ease $delay, opacity $time ease $delay,
		visibility $delay ease $delay;
	transform: scale(1);
	pointer-events: all;

	opacity: 1;
}

@mixin scaleOut($time: .25s, $delay: 0s) {
	visibility: hidden;

	transition: transform $time ease $delay, opacity $time ease $delay,
		visibility $time + $delay ease $time + $delay;
	transform: scale(0);
	pointer-events: none;

	opacity: 0;
}

@mixin transition() {
	transition: all .25s ease;
}

@mixin placeholder {
	&::-webkit-input-placeholder {
		@content;
	}

	&::-moz-placeholder {
		@content;
	}

	&:-moz-placeholder {
		@content;
	}

	&:-ms-input-placeholder {
		@content;
	}
}

@mixin center-flex {
	display: flex;
	align-items: center;
	justify-content: center;
}

@mixin indent-level($prop, $level, $dir: 'y', $negative: false) {
	$levelValues: map-get($levels-y, $level);

	@if $dir == 'x' {
		$levelValues: map-get($levels-x, $level);
	}

	@each $value in $levelValues {
		$levelValueIndex: index($levelValues, $value);

		@if $levelValueIndex == 1 {
			@if ($negative) {
				#{$prop}: -#{$value};
			}

 @else {
				#{$prop}: $value;
			}
		}

 @else {
			$breakpoint: nth($level-breakpoints, $levelValueIndex - 1);

			@include above($breakpoint) {
				@if ($negative) {
					#{$prop}: -#{$value};
				}

 @else {
					#{$prop}: $value;
				}
			}
		}
	}
}
