.types-change {
	display: flex;
	align-items: center;

	&__label {
		position: relative;

		display: flex;
		align-items: center;
		justify-content: center;

		width: 5rem;
		height: 5rem;

		cursor: pointer;
	}

	&__input {
		position: absolute;
		right: 0;
		left: 0;

		width: 1px;
		height: 1px;

		opacity: 0;
	}

	&__input:checked ~ &__icon {
		opacity: 1;
	}

	&__icon {
		transition: opacity #{$transition};

		opacity: .5;
	}
}
