.services-item {
	display: block;

	transition: box-shadow #{$transition};

	border: .1rem solid color(dark-10);
	background-color: color(white-main);

	&__block {
		height: 100%;
	}

	&__img {
		position: relative;

		height: 24.4rem;

		@include above($desktop) {
			height: 100%;
			min-height: 26.4rem;
		}

		@include above($big-desktop) {
			height: 100%;
			min-height: 19.4rem;
		}
	}

	&__bg {
		position: absolute;
		top: 50%;
		left: 50%;

		width: 100%;
		height: 100%;

		transform: translate(-50%, -50%);

		background-repeat: no-repeat;
		background-position: center;

		background-size: cover;
	}

	&__info {
		height: 100%;
		padding: 2rem 1.5rem;

		@include above($desktop) {
			padding: 3rem 3rem 3rem 2rem;
		}

		@include above($mid-desktop) {
			padding: 3rem 4rem;
		}
	}

	&__col--1 {
		@include above($desktop) {
			width: 36rem;
			min-width: 36rem;
		}
	}

	&--link {
		&:hover {
			@media (hover: hover) {
				box-shadow: 0 3px 30px rgba(7, 21, 10, .1);
			}
		}
	}

	&--no-bd {
		border: none;
	}

	&--md &__info {
		@include above($mid-desktop) {
			min-height: 26.4rem;
		}
	}

	&--md &__info {
		@include above($big-desktop) {
			padding-right: 14rem;
		}
	}
}
