.desktop-menu {
	position: fixed;
	z-index: 10;
	top: 6.8rem;
	right: 0;
	bottom: 0;
	left: 0;

	display: none;

	width: 100vw;

	transition: transform .6s ease;
	transform: translateY(-100%);
	pointer-events: none;

	background: color(white-main);

	@include above($desktop) {
		display: block;
	}

	@include above($mid-desktop) {
		top: 9rem;
	}

	&__wrapper {
		position: relative;

		height: 100%;
		padding: 4rem 14.8rem;

		@include above($mid-desktop) {
			padding: 4rem 17rem;
		}

		@include above($large-desktop) {
			padding: 4rem 20rem;
		}

		@include above($big-desktop) {
			padding: 4rem 26.5rem;
		}
	}

	&__back {
		position: absolute;
		top: 5rem;
		bottom: 0;
		left: 2rem;

		@include above($desktop) {
			left: 6rem;
		}

		@include above($mid-desktop) {
			left: 4rem;
		}

		@include above($large-desktop) {
			left: 5rem;
		}
	}

	&__info {
		position: absolute;
		top: 0;
		right: 20.8rem;
		bottom: 0;

		display: flex;
		align-items: center;

		width: 19rem;

		@include above($mid-desktop) {
			right: 21rem;
		}

		@include above($large-desktop) {
			right: 25rem;
		}

		@include above($big-desktop) {
			right: 31.5rem;
		}

		&::before {
			position: absolute;
			top: 0;
			bottom: 0;
			left: -6rem;

			display: block;

			content: '';

			border-left: .1rem solid color(dark-10);
		}
	}

	&__first-level {
		position: sticky;
		top: 0;
	}

	&__first-level-item {
		transition: visibility .3s, opacity .3s, transform .3s;
		visibility: hidden;
		transform: translateY(-10px);
		opacity: 0;

		.is-opened & {
			visibility: visible;
		
			transform: translateY(0);
		
			opacity: 1;
		
			@for $i from 1 through 7 {
				&:nth-child(#{$i}) {
					transition-delay: .4s + $i / 2 * .05s;
				}
			}
		}

	}

	&__second-level-list {
		max-width: 26rem;

		@include above($small-desktop) {
			max-width: 28rem;
		}

		@include above($large-desktop) {
			max-width: 30rem;
		}
	}

	&__second-level-group {
		position: fixed;

		width: 24rem;
	}

	&__second-level-item {
		.link--color--dark-main {
			.icon {
				* {
					fill: transparent;
				}
			}
		}
	}

	&__third-level {
		height: 100%;
	}

	&__third-level-list {
		column-count: 2;
		column-gap: 2rem;

		@include above($small-desktop) {
			column-gap: 4rem;
		}

		@include above($large-desktop) {
			column-gap: 6rem;
		}

		@include above($big-desktop) {
			column-count: 3;
		}
	}

	&__third-level-group {
		break-inside: avoid;
	}

	&.is-opened {
		transform: translateY(0);
		pointer-events: all;

		opacity: 1;
	}

	.slide-right-leave-active {
		transition: transform $transition-anim .15s, opacity $transition-anim ;
		.desktop-menu__second-level-item {
			pointer-events: none;
		}

	}

	.slide-right-enter-active {
		transition: transform $transition-anim, opacity $transition-anim .25s;
		.desktop-menu__second-level-item {
			pointer-events: none;
		}
	}

	.slide-right-enter {
		transform: translateY(-100%);

		opacity: 0;
	}

	.slide-right-enter-to {
		transform: translateY(0%);

		opacity: 1;
	}

	.slide-right-leave {
		transform: translateY(-100%);

		opacity: 0;
	}

	.slide-right-leave-to {
		transform: translateY(-100%);

		opacity: 0;
	}

	// .fade-enter-active,
	// .fade-leave-active {
	// 	transition: opacity .2s ease;
	// }

	// .fade-enter {
	// 	opacity: 0;
	// }

	// .fade-enter-to {
	// 	opacity: 1;
	// }

	// .fade-leave {
	// 	opacity: 0;
	// }

	// .fade-leave-to {
	// 	opacity: 0;
	// }

	.absolute-slide-left-leave-active {
		position: absolute;

		transition: transform $transition-anim, opacity $transition-anim;
	}

	.absolute-slide-left-enter-active {
		position: absolute;

		transition: transform $transition-anim .15s, opacity $transition-anim .15s;
	}


	.absolute-slide-left-enter {
		transform: translateY(100%);

		opacity: 0;
	}

	.absolute-slide-left-enter-to {
		transform: translateY(0%);

		opacity: 1;
	}

	.absolute-slide-left-leave {
		transform: translateY(100%);

		opacity: 0;
	}

	.absolute-slide-left-leave-to {
		transform: translateY(100%);

		opacity: 0;
	}
}
