.slider-arrows {
	display: flex;

	&__btn {
		outline: none;

		&.swiper-button-disabled {
			pointer-events: none;

			opacity: .3;
		}
	}

	&__btn:hover .icon {
		stroke: color(green-main);
	}

	&__btn .icon {
		width: 3.5rem;
		height: 3.5rem;

		transition: stroke #{$transition};
	}
}
