.question-block {
	&__inner {
		padding: 3rem 4.3rem;

		text-align: center;

		border: 1px solid color(green-main);
	}

	&__title {
		margin-bottom: 1rem;
	}

	&__desc {
		margin-bottom: 2.5rem;
	}

	&__btn {
		width: 100%;
		max-width: 25rem;
	}

	&--sm &__inner {
		padding: 3rem;
	}
}
