.footer {
	z-index: 100;
	overflow: hidden;
	background-color: color(white-main);
	border-top: 2px solid color(green-main);

	@include above($mid-desktop) {
		border-top: 1px solid color(dark-10);
	}

	&__wrapper {
		@include indent-level(padding-top, blue);
		@include indent-level(padding-bottom, green);

		@include below($mid-desktop) {
			@include indent-level(padding-top, green);
		}

		@include above($desktop) {
			padding-left: 2rem;
			padding-right: 2rem;
		}

		@include above($mid-desktop) {
			padding-left: 4rem;
			padding-right: 4rem;
		}
	}

	&__col--3 {
		order: -1;

		@include above($mid-desktop) {
			position: relative;

			order: 2;

			@include indent-level(padding-top, green);
		}

		&::before {
			@include above($mid-desktop) {
				position: absolute;
				top: 0;
				right: -150px;
				left: -150px;

				height: 2px;

				content: '';

				background-color: color(green-main);
			}
		}
	}

	&__info {
		display: flex;
		flex-direction: column;

		height: 100%;
	}

	&__leaflet {
		@include above($mid-desktop) {
			margin-top: auto;
			margin-bottom: -4px;
			padding-top: 5rem;
		}
	}

	&__nav {
		@include indent-level(margin-bottom, blue);
	}

	&__nav-title {
		margin-bottom: 1.5rem;
	}

	&__nav-item {
		margin-bottom: 1rem;

		&:last-of-type {
			margin-bottom: 0;
		}
	}

	&__bottom {
		padding-right: 1rem;

		@include above($mid-desktop) {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
	}
}
