@mixin title-1 {
	font-size: 3.5rem;
	line-height: 1.1;
	font-weight: $bold-weight;

	letter-spacing: 0;

	@include above($mid-desktop) {
		font-size: 5rem;
	}

	// С 1440 px до 1920 px размер шрифта Заголовка
	// пропорционально увеличивается с 50 до 70 px
	@include above($large-desktop) {
		font-size: 3.47vw;
	}

	@include above($extra-desktop) {
		font-size: 7rem;
	}
}

@mixin title-2 {
	font-size: 2.7rem;
	line-height: 1.1;
	font-weight: $bold-weight;

	letter-spacing: 0;

	@include above($mid-desktop) {
		font-size: 3.5rem;
	}
}

@mixin title-h3 {
	font-size: 2rem;
	line-height: 1.1;
	font-weight: $bold-weight;

	letter-spacing: 0;
}

@mixin title-h4 {
	font-size: 1.8rem;
	line-height: 1.4;
	font-weight: $bold-weight;

	letter-spacing: 0;
}

@mixin title-h5 {
	font-size: 1.6rem;
	line-height: 1.4;
	font-weight: $bold-weight;

	letter-spacing: 0;
}

@mixin title-h6 {
	font-size: 1.4rem;
	line-height: 1.4;
	font-weight: $bold-weight;

	letter-spacing: 0;
}

@mixin title-3 {
	@include title-h3;

	@include below($mid-desktop - 1) {
		font-size: 1.6rem;
	}
}

@mixin title-4 {
	@include title-h4;

	@include below($mid-desktop - 1) {
		font-size: 1.6rem;
	}
}

@mixin title-5 {
	@include title-h5;

	@include below($mid-desktop - 1) {
		font-size: 1.6rem;
	}
}

@mixin title-6 {
	@include title-h5;

	@include below($mid-desktop - 1) {
		font-size: 1.4rem;
	}
}

@mixin text-base-xl {
	font-size: 2.7rem;
	line-height: 1.1;

	@include below($mid-desktop - 1) {
		font-size: 1.8rem;
	}
}

@mixin text-base-large {
	font-size: 1.6rem;
	line-height: 1.4;
}

@mixin text-base-medium {
	font-size: 1.4rem;
	line-height: 1.4;
}

@mixin text-base-small {
	font-size: 1.2rem;
	line-height: 1.4;
}

@mixin text-base-xsmall {
	font-size: 1rem;
	line-height: 1.2;
}

.t-base {
	color: color(dark-main);

	&--size--h1 {
		@include title-1();
	}

	&--size--h2 {
		@include title-2();
	}

	&--size--h3 {
		@include title-h3();
	}

	&--size--h4 {
		@include title-h4();
	}

	&--size--h5 {
		@include title-h5();
	}

	&--size--title-3 {
		@include title-3();
	}

	&--size--title-4 {
		@include title-4();
	}

	&--size--title-5 {
		@include title-5();
	}

	&--size--title-6 {
		@include title-6();
	}

	&--size--xl {
		@include text-base-xl();
	}

	&--size--large {
		@include text-base-large();
	}

	&--size--medium {
		@include text-base-medium();
	}

	&--size--small {
		@include text-base-small();
	}

	&--size--xsmall {
		@include text-base-xsmall();
	}

	&--weight--bold {
		font-weight: $bold-weight;
	}

	&--color--dark-55 {
		color: color(dark-55);
	}

	&--color--dark-25 {
		color: color(dark-25);
	}

	&--color--dark-40 {
		color: color(dark-40);
	}

	&--color--dark-70 {
		color: color(dark-70);
	}

	&--color--green-main {
		color: color(green-main);
	}

	&--color--white-main {
		color: color(white-main);
	}

	&--color--red-main {
		color: color(red-main);
	}

	&--align--center {
		text-align: center;
	}
}

.v-indent {
	&--purple {
		&:not(:last-child) {
			@include indent-level(margin-bottom, purple);
		}
	}

	&--blue {
		&:not(:last-child) {
			@include indent-level(margin-bottom, blue);
		}
	}

	&--green {
		&:not(:last-child) {
			@include indent-level(margin-bottom, green);
		}
	}

	&--yellow {
		&:not(:last-child) {
			@include indent-level(margin-bottom, yellow);
		}
	}

	&--orange {
		&:not(:last-child) {
			@include indent-level(margin-bottom, orange);
		}
	}

	&--red {
		&:not(:last-child) {
			@include indent-level(margin-bottom, red);
		}
	}
}

.rouble-sign {
	font-family: 'Helvetica Neue', sans-serif;
}

@mixin ul-li {
	position: relative;

	padding-left: 2.6rem;
	break-inside: avoid-column;

	@include indent-level(margin-bottom, orange);

	&::before {
		position: absolute;
		top: .9rem;
		left: .9rem;

		width: .6rem;
		height: .6rem;

		content: '';

		-webkit-border-radius: 50%;
		-moz-border-radius: 50%;
		border-radius: 50%;
		background-color: color(green-main);
	}

	&:last-of-type {
		margin-bottom: 0;
	}
}

.ul-base {
	li {
		@include ul-li;

		&.t-base--size--medium {
			&::before {
				top: .6rem;
			}
		}

		strong {
			font-weight: bold;
		}
	}

	&--sm--column-count-2 {
		@include above($desktop) {
			column-count: 2;
		}
	}

	&--column-count {
		&--3 {
			column-count: 3;
		}
	}
}

.ol-base {
	padding-left: 2.7rem;

	counter-reset: item;

	> li {
		position: relative;

		&::before {
			position: absolute;
			top: 0;
			left: -2.7rem;

			display: block;

			content: counter(item) '.';
			counter-increment: item;
		}
	}
}

.table-base {
	display: block;
	overflow-x: auto;

	max-width: 100%;

	text-align: left;

	th {
		padding: 1.6rem;
	}

	&--sticky-fr {
		th:first-child {
			position: sticky;
			left: 0;

			box-shadow: .2rem 0 .4rem rgba(66, 59, 87, .1);

			@include above($desktop) {
				box-shadow: none;
			}
		}
	}

	thead {
		color: color('monochrome-medium');

		th {
			background-color: color('monochrome-thin');
		}
	}

	tbody {
		tr {
			transition: background-color $transition;

			border-bottom: .1rem solid color('monochrome-light');

			&:hover {
				& > th {
					@include above($desktop) {
						background-color: color('blue-light');
					}
				}
			}
		}

		th {
			white-space: nowrap;

			background-color: color('monochrome-zero');
		}
	}

	&--w-100 {
		@include above($tablet) {
			display: table;

			width: 100%;
		}
	}
}

.break-line {
	border: .1rem solid color(dark-10);

	@include indent-level(margin-bottom, green);
	@include indent-level(margin-top, green);
}
