.section-nav {
	padding-top: 6rem;
	position: relative;

	@include above($desktop) {
		padding-top: 10rem;
	}

	@include above($mid-desktop) {
		padding-top: 14rem;
	}

	@include above($large-desktop) {
		padding-top: 22rem;
	}

	&__row {
		align-items: flex-start;
	}

	&__aside {
		display: none;
		@include indent-level(padding-bottom, purple);

		@include above($mid-desktop) {
			display: block;
			position: absolute;
			height: 100%;
			left: 0;
			top: 0;
			z-index: 2;
			padding-top: 14rem;
		}

		@include above($large-desktop) {
			padding-top: 22rem;
		}
	}


	&__aside-wrapper {
		position: sticky;
		top: 9rem;
		z-index: 2;
	}

	&__nav {
		@include above($mid-desktop) {
			width: 17.5rem;
		}

		@include above($large-desktop) {
			width: 19.4rem;
		}

		@include above($big-desktop) {
			width: 26.4rem;
		}
	}

	&__section {
		// @include above($mid-desktop) {
		// 	width: 80%;
		// }

		// @include above($big-desktop) {
		// 	width: 72%;
		// }

		// @include above($extra-desktop) {
		// 	width: 80%;
		// }

		padding-top: 6rem;

		@include above($desktop) {
			padding-top: 10rem;
		}

		@include above($mid-desktop) {
			padding-top: 14rem;
			padding-left: 17.5rem;
		}

		@include above($large-desktop) {
			padding-left: 19.4rem;
			padding-top: 22rem;
		}

		@include above($big-desktop) {
			//padding-top: 22rem;
			padding-left: 26.4rem;
		}
	}

	&__content-section {
		@include above($mid-desktop) {
			padding-left: 17.5rem;
		}

		@include above($large-desktop) {
			padding-left: 19.4rem;
		}

		@include above($big-desktop) {
			padding-left: 26.4rem;
		}
	}

	&__section-wrapper {
		@include above($mid-desktop) {
			padding-right: 13rem;
		}

		@include above($large-desktop) {
			padding-right: 15rem;
		}

		@include above($big-desktop) {
			padding-right: 27rem;
		}
	}


	&__header {
		@include indent-level(margin-bottom, blue);
	}

	&__title {
		@include indent-level(margin-bottom, orange);
	}

	&__desc {
		@include indent-level(margin-bottom, yellow);
	}

	&__link {
		@include above($tablet) {
			display: flex;
			align-items: center;
		}
	}

	&__link-item {
		margin-bottom: 2rem;

		@include above($tablet) {
			display: flex;

			margin-bottom: 0;
			margin-right: 4rem;
		}

		&:last-of-type {
			margin-bottom: 0;

			@include above($tablet) {
				margin-right: 0;
			}
		}
	}

	&__link--lg {
		@include above($tablet) {
			display: block;
		}

		@include above($large-desktop) {
			//display: flex;
			//align-items: center;
		}
	}

	&__link--lg &__link-item {
		@include above($tablet) {
			margin-bottom: 2rem;
			margin-right: 0;
		}

		@include above($large-desktop) {
			//margin-bottom: 0;
			//margin-right: 4rem;
		}

		&:last-of-type {
			@include above($tablet) {
				margin-bottom: 0;
			}

			@include above($large-desktop) {
				margin-right: 0;
			}
		}
	}

	&__link--lg &__link-link {
		margin-right: 2rem;
		&:last-of-type {
			margin-right: 0;
		}
	}


	&__link-title {
		@include above($tablet) {
			margin-right: 1rem;
			flex-shrink: 0;
		}
	}

	&__header &__header--col-1 {

		@include above($desktop) {
			flex-grow: 1;
		}
		@include above($big-desktop) {
			flex-grow: 1;
		}
	}

	&__header--col-2 {
		justify-content: flex-end;
		align-items: flex-start;

		padding-top: 2rem;
	}

	&__img {
		width: 100%;
		max-width: 22.5rem;

		@include above($mid-desktop) {
			max-width: 26.6rem;
		}

		@include above($big-desktop) {
			max-width: 30rem;
		}
	}

	&__info {
		@include indent-level(margin-top, purple);
	}

	&__info--bg {
		position: relative;
	}

	&__info-bg {
		position: absolute;
		z-index: -1;
		top: 0;
		right: -2rem;
		left: -2rem;

		height: 100%;

		background-repeat: no-repeat;
		background-size: 100% auto;

		@include above($desktop) {
			right: -6rem;
			left: -6rem;
		}

		@include above($mid-desktop) {
			right: -22%;
			left: -22%;
		}

		@include above($big-desktop) {
			right: -23%;
			left: -30%;
		}
	}

	&__files-item {
		margin-bottom: 1rem;

		&:last-of-type {
			margin-bottom: 0;
		}
	}

	&__question-block {
		// @include indent-level(padding-bottom, purple);
		@include indent-level(margin-top, purple, 'y', true);
	}


	&__filter {
		@include indent-level(padding-bottom, green);
	}

	&__filter--padding {
		@include above($mid-desktop) {
			padding-right: 4rem;
		}
	}

	&__pagination {
		display: flex;
		justify-content: center;
		@include indent-level(padding-top, blue);

		@include above($desktop) {
			justify-content: flex-start;
		}
	}

	&__btns {
		display: flex;
		align-items: center;

		@include indent-level(padding-top, blue);

		.button {
			&:first-of-type {
				margin-right: 1rem;

				@include above($mid-desktop) {
					margin-right: 2rem;
				}
			}

			@include below($phone-landscape - 1) {
				flex-grow: 1;

				padding-right: 1.5rem;
				padding-left: 1.5rem;
			}

			@include below($phone - 1) {
				padding-left: .5rem;
				padding-right: .5rem;
			}
		}
	}

	&__btns--fixed {
		.button {
			&:first-of-type {
				@include above($mid-desktop) {
					margin-right: 1rem;
				}
			}
		}
	}

	&__aside-block {
		width: 15rem;

		transform: translateX(.1rem);

		box-shadow: 0 3px 30px rgba(7, 21, 10, .1);

		img {
			max-width: 100%;
		}
	}

	&--bg {
		background-position: right top;
		background-repeat: no-repeat;

		@include below($desktop) {
			// временно поставил important.  потом решиим
			background: none !important;
		}
	}

	&--pt {
		padding-top: 10rem;

		@include above($mid-desktop) {
			padding-top: 14rem;
		}

		@include above($big-desktop) {
			padding-top: 22rem;
		}
	}

	&__body {
		@include indent-level(padding-bottom, purple);
	}

	&__body &__content-section:last-of-type{
		padding-bottom: 0;
	}

	&--sm {

		padding-top: 10rem;

		@include above($desktop) {
			padding-top: 12rem;
		}

		@include above($mid-desktop) {
			padding-top: 19rem;
		}

		@include above($large-desktop) {
			padding-top: 19rem;
		}
	}

	&__link-items {
		display: flex;
		flex-wrap: wrap;
	}

}


