.form-labels {
	&__row {
		@include above($tablet) {
			margin-left: -1rem;
			margin-right: -1rem;
		}
	}

	&__col {
		margin-bottom: 2rem;

		@include above($tablet) {
			padding-left: 1rem;
			padding-right: 1rem;
			margin-bottom: 0;
		}

		&:last-of-type {
			margin-bottom: 0;
		}
	}
}
