.request-list {
	&__item {
		position: relative;

		margin-left: -2rem;
		margin-right: -2rem;

		border-bottom: 1px solid color(dark-10);
		background-color: color(white-main);

		@include above($desktop) {
			margin-left: 0;
			margin-right: 0;
		}

		&:first-of-type {
			border-top: 1px solid color(dark-10);
		}
	}

	&__item-col--1 {
		padding-left: 2rem;
		padding-right: 2rem;

		@include indent-level(padding-top, yellow);
		@include indent-level(padding-bottom, yellow);

		@include above($desktop) {
			display: flex;
			align-items: center;

			padding-left: 3rem;
		}

		@include above($large-desktop) {
			width: calc(100% - 44.4rem);
		}
	}

	&__item-col--2 {
		@include above($mid-desktop) {
			width: 44.4rem;
			min-width: 44.4rem;
			max-width: 44.4rem;
		}
	}

	&__item-delete {
		position: absolute;
		z-index: 3;
		top: 1rem;
		right: 1.2rem;

		display: flex;
		align-items: center;
		justify-content: center;

		width: 20px;
		height: 20px;

		@include above($desktop) {
			top: 50%;

			transform: translateY(-50%);
		}
	}

	&__item-delete .icon {
		width: 1.8rem;
		height: 1.8rem;
	}
}
