.question-btn {
	padding-right: 1.4rem;
	padding-left: 1.4rem;

	border: .2rem solid color(green-main);
	border-radius: 2rem;
	background-color: color(white-main);

	&:hover {
		@media (hover: hover) {
			color: color(white-main);
			border-width: .2rem;
			border-color: color(green-main);
			background-color: color(green-main);
		}

		.button__wrapper::before {
			@media (hover: hover) {
				background-color: color(white-main);
			}
		}
	}

	.button__wrapper {
		font-size: inherit;

		position: relative;

		padding-left: 2rem;

		&::before {
			position: absolute;
			top: .6rem;
			left: 0;

			width: .6rem;
			height: .6rem;

			content: '';
			transition: background-color #{$transition};

			border-radius: 50%;
			background-color: color(green-main);
		}
	}

	.button__text {
		font-size: 1.2rem;
	}
}
