.modal-slider {
    position: relative;

    overflow: hidden;

    width: 100%;
    height: 0;
    padding-top: 56.25%;

    &__container {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
    }

    &__button {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 4rem;
        height: 4rem;
        border-radius: 50%;
        opacity: 1;
        background: rgba(color(white-main),.3);
        transition: opacity $transition;
        z-index: 1;
        justify-content: center;
        &.swiper-button-disabled {
			pointer-events: none;

			opacity: 0;
		}
    }

    &__button--left {
        left: 1rem;
    }

    &__button--right {
        right: 1rem;
    }

    &__slide {
        height: 100%;
        width: 100%;
    }

    &__img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}