.advantages-item {
	&__inner {
		position: relative;

		padding-left: 3rem;
	}

	&__number {
		font-size: 11rem;
		font-weight: 800;
		line-height: 11rem;

		position: absolute;
		top: -3.4rem;
		left: -.6rem;

		color: color(blue-middle);
	}

	&__info {
		position: relative;
		z-index: 1;
	}

	&__title {
		padding-right: 2rem;
	}

	&--min-height &__info{
		min-height: 4rem;


		@include above($desktop) {
			min-height: auto;
		}
	}
}
