.distributors {
	&__col--1 {
		@include indent-level(margin-bottom, yellow);

		@include above($large-desktop) {
			max-width: 28rem;
			width: 28rem;
		}
	}

	&__img {
		display: flex;
		align-items: center;
		justify-content: center;

		width: auto;
		max-width: 20rem;

		@include above($desktop) {
			width: 100%;
			max-width: none;
		}

		img {
			width: auto;
			max-width: 100%;
		}
	}
}
