.row--margin {
	&--60 {
		margin-bottom: -2rem;

		@include above($mid-desktop) {
			margin-bottom: -4rem;
		}

		@include above($large-desktop) {
			margin-bottom: -6rem;
		}
	}

	&--60 > &__col {
		margin-bottom: 2rem;

		@include above($mid-desktop) {
			margin-bottom: 4rem;
		}

		@include above($large-desktop) {
			margin-bottom: 6rem;
		}
	}

	&--xl {
		margin-bottom: -4rem;

		@include above($mid-desktop) {
			margin-bottom: -6rem;
		}
	}

	&--xl > &__col {
		margin-bottom: 4rem;

		@include above($mid-desktop) {
			margin-bottom: 6rem;
		}
	}


	&--xxl {
		margin-bottom: -5rem;
	}

	&--xxl > &__col {
		margin-bottom: 5rem;
	}

	&--blue {
		@include indent-level(margin-bottom, blue, 'y', true);
	}

	&--blue > &__col {
		@include indent-level(margin-bottom, blue);
	}

	&--green {
		@include indent-level(margin-bottom, green, 'y', true);
	}

	&--green > &__col {
		@include indent-level(margin-bottom, green);
	}

	&--orange {
		@include indent-level(margin-bottom, orange, 'y', true);
	}

	&--orange > &__col {
		@include indent-level(margin-bottom, orange);
	}

	&--yellow {
		@include indent-level(margin-bottom, red, 'y', yellow);
	}

	&--yellow > &__col {
		@include indent-level(margin-bottom, yellow);
	}

	&--red {
		@include indent-level(margin-bottom, red, 'y', true);
	}

	&--red > &__col {
		@include indent-level(margin-bottom, red);
	}
}
