.burger {
	&__line {
		display: block;

		width: 1.4rem;
		height: .2rem;
		margin: .2rem auto;

		transition: all #{$transition};

		border-radius: .1rem;
		background-color: color(white-main);

		.is-active-burger &:nth-child(2) {
			opacity: 0;
		}

		.is-active-burger &:nth-child(1) {
			transform: translateY(.4rem) rotate(45deg);
		}

		.is-active-burger &:nth-child(3) {
			transform: translateY(-.4rem) rotate(-45deg);
		}
	}

	&--size--big &__line {
		width: 2rem;
		margin: .4rem auto;
	}
}
