// Colors
$base-colors: (
	'white-main': #ffffff,
	'dark-main': #07150a,
	'dark-85': #2c382f,
	'dark-70': #515b54,
	'dark-55': #777e78,
	'dark-40': #9ca19d,
	'dark-25': #c1c4c2,
	'dark-10': #e6e8e7,
	'dark-bg': #f2f3f3,
	'green-main': #7ac30f,
	'green-hover': #8ecc33,
	'green-pressed': #a5d65c,
	'green-bg': #f2f9e7,
	'red-main': #d31235,
	'blue-main': #00758a,
	'blue-bg': #f4f9fa,
	'blue-sky': #f2f8f9,
	'blue-light': #d9eaed,
	'blue-middle': #EBF7FA,
	'yellow-main': #f4b235,
	'yellow-bg': #fef7eb,
	'yellow-hover': #f6be53,
	'yellow-light': #f8d186,
);

@function color($color) {
	@return map-get($base-colors, $color);
}

$color-accent: color(dark-85);
$color-accent-hover: color(dark-70);
$color-accent-active: color(dark-55);

$color-base: color(green-main);
$color-base-hover: color(green-hover);
$color-base-active: color(green-pressed);

$color-error: color(red-main);
