.achievement {
	margin-bottom: -3rem;

	&__row {
		@include above($tablet) {
			justify-content: space-between;
		}

		@include above($large-desktop) {
			justify-content: center;
		}
	}

	&__col {
		margin-bottom: 3rem;

		@include above($tablet) {
			width: auto;
			max-width: 20%;
		}

		@include above($large-desktop) {
			min-width: 14rem;
			max-width: none;
			margin-right: 4rem;
		}

		&:last-of-type {
			@include above($large-desktop) {
				margin-right: 0;
			}
		}
	}
}
