@import './config';

.file-uploader {
	width: 100%;

	&__wrapper {
		display: flex;
		align-items: center;
		flex-direction: column;

		width: 100%;

		@include above($tablet) {
			display: flex;
			align-items: center;
			flex-wrap: wrap;
		}
	}

	&__control {
		@include hidden();
	}

	&__thumb {
		max-height: 5rem;
		max-width: 5rem;
	}

	&__list {
		font-size: 1.2rem;
		line-height: 1;

		display: flex;
		align-items: center;
		flex: 1;
		flex-wrap: wrap;

		margin-top: 0;
		margin-bottom: -.5rem;
		padding-left: 0;

		@include above($tablet) {
			margin-left: 2.3rem;
		}
	}

	&__item {
		position: relative;

		display: flex;
		align-items: center;

		margin-bottom: .5rem;

		@include fadeOut;

		&:not(:last-child) {
			margin-right: 2rem;
		}

		&.is-show {
			@include fadeIn;
		}
	}

	&__error {
		font-size: 1.2rem;
		line-height: 1;

		visibility: hidden;

		margin-top: .5rem;
		margin-bottom: .5rem;

		// @include typography(text, xsmall);

		opacity: 0;
		color: $file-uploader-error-color;
	}

	&__item-name {
		font-size: 1.6rem;
		font-weight: 400;
		line-height: 1;

		color: #323c46;
	}

	&__remove-btn {
		position: absolute;
		top: 0;
		right: 0;

		display: flex;
		align-items: center;
		justify-content: center;

		width: 2.4rem;
		height: 2.4rem;

		transform: translate(50%, -50%);

		border-radius: 100%;

		background: color(blue-light);

		.icon {
			width: 1.2rem;
			fill: color(blue-main);
		}
	}

	&.is-error &__error,
	&--error &__error {
		@include fadeIn;
	}

	&--error-hanging {
		margin-bottom: 0;
	}
}
