.search-results {
	display: flex;
	flex-direction: column;

	width: 100%;

	&__wrapper {
		flex: 1;

		width: 100%;
		padding: 1.5rem 2rem 0;

		@include above($desktop) {
			padding: 5rem 4rem 0;
		}
	}

	&__group {
		padding-bottom: 3rem;

		border-bottom: .1rem solid color('dark-10');

		&:not(:first-child) {
			padding-top: 3rem;
		}
	}

	&__group-title {
		margin-bottom: 1.5rem;
	}

	&__items {
		margin-bottom: 2rem;
	}

	&__item {
		display: flex;
		justify-content: space-between;

		margin-bottom: 2rem;
	}

	&__item-link {
		.link__text {
			display: -webkit-box;
			overflow: hidden;

			text-overflow: ellipsis;
			-webkit-line-clamp: 2;
			-webkit-box-orient: vertical;

			@include above($desktop) {
				-webkit-line-clamp: 1;
			}
		}
	}

	&__item-info {
		flex-shrink: 0;

		margin-left: 2rem;

		@include above($desktop) {
			margin-left: 4rem;
		}
	}

	&__see-all {
		display: flex;
		align-items: center;
		flex-shrink: 0;
		justify-content: center;

		height: 5rem;
		padding: 1.5rem 0;

		text-align: center;

		background: color('white-main');
		// margin: 0 -2rem;
		// @include above($desktop) {
		//     margin: 0 -4rem;
		// }
		&.is-sticky {
			box-shadow: 0 -.4rem .8rem rgba(color('dark-main'), .1);
		}
	}
}
