@import './config';

.input {
	font-size: $input-font-size;

	position: relative;

	width: 100%;

	&__wrapper {
		position: relative;
	}

	&__icon {
		position: absolute;
		top: calc(50% - 1rem);
		right: 1.3rem;

		height: 2rem;

		pointer-events: none;
	}

	&__control {
		font-size: inherit;

		position: relative;

		display: block;

		width: 100%;
		height: 5rem;
		padding-right: 1.6rem;
		padding-left: 1.6rem;

		transition: font-size #{$transition}, padding-top #{$transition},
			border-color #{$transition};
		transform: translateZ(0);

		color: transparent;
		border: .1rem solid $input-border-color;
		background-color: $input-control-bg-color;

		&:hover {
			border-color: $input-hover-border-color;
		}

		&::placeholder {
			color: transparent;
		}

		&.error {
			border-color: $input-border-error-color;
		}

		.is-focused &,
		.is-changed & {
			padding-top: 1.6rem;

			color: $input-text-color;
		}

		.is-focused &,
		&:focus {
			outline: none;
			border-color: $input-focus-border-color;
		}

		&[disabled] {
			cursor: not-allowed;
		}
	}

	&__control--textarea {
		min-height: 9rem;
		padding-bottom: 1.5rem;

		resize: none;

		.is-focused &,
		.is-changed & {
			padding-top: 2.3rem;
		}
	}

	&__control--textarea-md {
		min-height: 15rem;
	}

	&__placeholder {
		left: 1.6rem;

		overflow: hidden;

		width: calc(100% - 1.6rem);

		transition: transform #{$transition};
		transform-origin: top left;
		white-space: nowrap;
		text-overflow: ellipsis;
		pointer-events: none;

		color: $input-placeholder-color;
		will-change: transform;

		@include center($horiz: false);
	}

	&__control--textarea ~ &__placeholder {
		top: 1.6rem;

		transform: none;
	}

	&--suggestions {
		&.is-focused.is-changed {
			&::before {
				position: absolute;
				z-index: 10000;
				bottom: 0;
				left: .1rem;

				display: block;

				width: calc(100% - .2rem);
				height: .1rem;

				content: '';

				background-color: $input-suggestions-bg-color;
			}

			input {
				border: .1rem solid $input-suggestions-border-color;
			}
		}
	}

	&--search &__control {
		padding-left: 4rem;

		border-radius: 2.5rem;
		background: $input-control-bg-color
			url("data:image/svg+xml,%3Csvg width='17' height='17' viewBox='0 0 17 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M13.7047 12.2558C16.1186 9.30982 15.9505 4.95568 13.2006 2.20573C10.2716 -0.723199 5.5229 -0.723199 2.59397 2.20573C-0.334962 5.13467 -0.334961 9.8834 2.59397 12.8123C5.34391 15.5623 9.69803 15.7303 12.644 13.3165L15.6754 16.3479C15.9683 16.6408 16.4432 16.6408 16.7361 16.3479C17.029 16.055 17.029 15.5801 16.7361 15.2872L13.7047 12.2558ZM12.1399 3.26639C14.483 5.60954 14.483 9.40853 12.1399 11.7517C9.79673 14.0948 5.99774 14.0948 3.65459 11.7517C1.31144 9.40853 1.31144 5.60954 3.65459 3.26639C5.99774 0.923245 9.79673 0.923246 12.1399 3.26639Z' fill='%23D2D0D7'/%3E%3C/svg%3E%0A")
			no-repeat 1.1rem center / 1.7rem;
	}

	&--search &__placeholder {
		left: 4rem;

		width: calc(100% - 4rem);
	}

	.is-focused &__placeholder,
	.is-changed &__placeholder {
		transform: translateY(-1.9rem) scale(.85);
	}

	.is-focused &__control--textarea ~ &__placeholder,
	.is-changed &__control--textarea ~ &__placeholder {
		transform: translateY(-1.1rem) scale(.85);
	}

	label.error {
		position: absolute;
		top: 100%;
		left: 0;

		color: $input-label-error-color;

		@include text-base-small;
	}
}
