.section-files {
	&__item {
		margin-bottom: 2rem;

		&:last-of-type {
			margin-bottom: 0;
		}
	}

	&--sm {
		margin-bottom: -1rem;
	}

	&--sm &__item {
		margin-bottom: 1rem;

		@include above($tablet) {
			margin-right: 4rem;
		}

		&:last-of-type {
			@include above($tablet) {
				margin-right: 0;
			}
		}
	}
}
