.visit-card {
	width: 100%;
	height: 100%;
	min-height: 13rem;
	padding: 2rem;

	transition: box-shadow $transition;

	border: .1rem solid color(dark-10);
	border-left-width: .2rem;
	border-left-color: color(green-main);
	background: color(white-main);

	&:hover {
		@media (hover: hover) {
			z-index: 1;

			box-shadow: 0 .3rem 3rem rgba(color(dark-main), .1);
		}
	}

	&__wrapper {
		display: flex;
		flex-direction: column;
		justify-content: space-between;

		height: 100%;
	}

	&--bg--blue {
		background: color(blue-bg);
	}
}
