.slider-block {
	&__init {
		display: flex;
		flex-wrap: nowrap;

		margin-bottom: 2rem;

		@include padding-x(2rem);
	}

	&__controls {
		@include indent-level(margin-top, green);
	}

	&__init--custom {
		overflow: auto;

		margin-bottom: 0;
		padding-left: 0;
		padding-right: 0;
	}

	&__slide--custom {
		&:first-of-type {
			padding-left: 2rem;
		}

		&:last-of-type {
			padding-right: 2rem;
		}
	}

	&__init--md-visible {
		@include above($desktop) {
			overflow: visible;
			flex-wrap: nowrap;
		}
	}

	&__init--md-visible &__slide {
		&:first-of-type {
			@include above($desktop) {
				padding-left: 1rem;
			}

			@include above($mid-desktop) {
				padding-left: 2rem;
			}

			@include above($large-desktop) {
				padding-left: 3rem;
			}
		}

		&:last-of-type {
			@include above($desktop) {
				padding-right: 1rem;
			}

			@include above($mid-desktop) {
				padding-right: 2rem;
			}

			@include above($large-desktop) {
				padding-right: 3rem;
			}
		}
	}

	&__slide--xxl-20 {
		@include above($big-desktop) {
			width: 20%;
		}
	}

	&--custom {
		margin-left: -1rem;
		margin-right: -1rem;

		@include above($desktop) {
			margin-left: 0;
			margin-right: 0;
		}
	}

	&--overflow &__init {
		margin-left: -2rem;
		margin-right: -2rem;

		@include above($desktop) {
			margin-left: -6rem;
			margin-right: -6rem;
		}

		@include above($mid-desktop) {
			margin-left: -13rem;
			margin-right: -13rem;
		}

		@include above($large-desktop) {
			margin-left: -15rem;
			margin-right: -15rem;
		}

		@include above($big-desktop) {
			margin-left: -19rem;
			margin-right: -19rem;
		}
	}

	&--overflow .swiper-slide {
		height: auto;
	}

	&--overflow &__slide {
		width: 30rem;

		@include above($tablet) {
			width: 60rem;
		}

		@include above($desktop) {
			width: 67rem;
		}

		@include above($mid-desktop) {
			width: 76rem;
		}

		@include above($large-desktop) {
			width: 84rem;
		}

		@include above($big-desktop) {
			width: 114rem;
		}
	}


	&--overflow &__slide--md {
		@include above($mid-desktop) {
			width: 73rem;
		}

		@include above($large-desktop) {
			width: 73rem;
		}

		@include above($big-desktop) {
			width: 73rem;
		}
	}
}
