.achievement-item {
	&__number {
		font-size: 4.5rem;
	}

	&--type-2 {
		display: flex;
		align-items: center;
	}

	&--type-2  &__number{
		font-size: 4rem;
		margin-right: 2rem;
		width: 12rem;
		text-align: left;
		white-space: nowrap;

		@include above($phone-landscape) {
			width: auto;
		}
	}


	&--type-2  &__text{
		text-align: left;
	}
}
