.img-responsive {
	display: block;

	max-width: 100%;
	height: auto;
}

.is-hidden {
	display: none !important;
}

.visually-hidden {
	position: absolute;

	clip: rect(0 0 0 0);

	width: 1px;
	height: 1px;
	margin: -1px;
}

img.lazy:not(.loaded) {
	background: url('../img/loader.gif') no-repeat center center / 5rem;
}

.slick-list {
	min-width: 100%;
}

.slick-slider {
	font-size: 0;
}

.slick-slide {
	font-size: 0;

	&:focus,
	a {
		outline: none;
	}
}

@each $base-color-title, $base-color-hash in $base-colors {
	.bg-demo-#{$base-color-title} {
		background: color($base-color-title);
	}
}

.color-demo {
	height: 60px;
	width: 100%;
}

.ps {
	position: relative;

	.ps__rail-y {
		// right: 0.5rem !important;
		// left: auto !important;
		background-color: transparent !important;
		outline: none;

		.ps__thumb-y {
			background-color: color('dark-55');
		}
	}

	.ps__rail-x {
		background-color: transparent !important;
		outline: none;

		.ps__thumb-y {
			background-color: color('dark-55');
		}
	}
}

.is-overlay {
	position: fixed;
	z-index: 300;
	top: 0;
	left: 0;

	display: block;

	width: 100%;
	height: 100%;

	content: '';
	transition: opacity $transition;
	pointer-events: none;

	opacity: 0;
	background: rgba(color('dark-main'), .6);

	&.is-active {
		pointer-events: auto;

		opacity: 1;
	}
}

.flex-centering {
	display: flex;

	height: 100%;
	width: 100%;
}

.flex-centering--vertical {
	align-items: center;
}

.flex-centering--horizontal {
	justify-content: center;
}

.is-float {
	position: fixed;
	z-index: 300;
}

.is-float--right-bottom {
	bottom: 3rem;
	right: 2rem;

	@include above($mid-desktop) {
		right: 4rem;
	}

	@include above($large-desktop) {
		right: 5rem;
	}

	&.is-bottom {
		position: absolute;
		bottom: 12.2rem;

		@include above($mid-desktop) {
			bottom: 37rem;
		}

		@include above($large-desktop) {
			bottom: 40rem;
		}
	}
}

.is-float--right-bottom--on-product {
	bottom: 10rem;

	&.is-bottom {
		bottom: 19.2rem;

		@include above($mid-desktop) {
			bottom: 44rem;
		}

		@include above($large-desktop) {
			bottom: 47rem;
		}
	}
}

.v-cloak-opacity {
	display: block;
	transition: opacity $transition;

	opacity: 1;

	&[v-cloak] {
		opacity: 0;
	}
}

.w-100 {
	width: 100% !important;
}

.overflow-hidden {
	overflow: hidden;
}

.not-in-visible {
	transition: all .25s ease;

	opacity: 1;


	&.is-visible {
		pointer-events: none;

		opacity: 0;
	}
}

.animation-slide-up {
	transition: opacity 0.4s, transform .8s;
	transition-delay: .25s;
	will-change: transform, opacity;

	&.is-init-animation:nth-child(odd){
		transform: translateY(2rem);
		opacity: 0;

		@include above($mid-desktop) {
			transform: translateY(3rem);
		}

		@include above($large-desktop) {
			transform: translateY(6rem);
		}
	}

	&.is-init-animation:nth-child(even) {
		transform: translateY(4rem);
		opacity: 0;

		@include above($mid-desktop) {
			transform: translateY(6rem);
		}

		@include above($large-desktop) {
			transform: translateY(12rem);
		}
	}
}

.text-align {
	&--center {
		text-align: center;
	}
	&--right {
		text-align: right;
	}
}

.is-loading {
	pointer-events: none;

	opacity: .5;
}

.w-auto {
	width: auto !important;
}

.ml-1 {
	margin-left: 1rem !important;
}

.fs-16 {
	font-size: 1.6rem !important;
}

.mb-30 {
	margin-bottom: 3rem;
}

.text-uppercase {
	text-transform: uppercase;
}
