.publications {
	&__row {
		@include indent-level(margin-bottom, blue, 'y', true);
	}

	&__col {
		@include indent-level(margin-bottom, blue);
	}

	&__col--xxl-20 {
		@include above($big-desktop) {
			width: 20%;
			min-width: 20%;
		}
	}

	&--sm-col &__row {
		@include indent-level(margin-bottom, green, 'y', true);
	}

	&--sm-col &__col {
		@include indent-level(margin-bottom, green);
	}
}
