.news {
	background-size: contain;
	background-position: left top;

	.slider-block__init--custom {
		@include above($desktop) {
			flex-wrap: wrap;
			overflow: visible;
		}
	}

	.slider-block__slide--custom {
		&:first-of-type {
			@include above($desktop) {
				padding-left: 1rem;
			}

			@include above($mid-desktop) {
				padding-left: 2rem;
			}

			@include above($large-desktop) {
				padding-left: 3rem;
			}
		}

		&:last-of-type {
			@include above($desktop) {
				padding-right: 1rem;
			}

			@include above($mid-desktop) {
				padding-right: 2rem;
			}

			@include above($large-desktop) {
				padding-right: 3rem;
			}
		}
	}
}
