.form {
	$this: &;

	&__control {
		&:not(:last-child) {
			@include indent-level(margin-bottom, yellow);
		}

		&--col {
			width: 100%;

			padding: 0 .8rem;

			@include indent-level(margin-bottom, yellow);

			&:last-child {
				@include indent-level(margin-bottom, yellow);
			}
			
			&--33 {
				@include above($tablet) {
					flex: 0 0 33.333%;

					max-width: 33.333%;
				}
			}

			&--50 {
				@include above($tablet) {
					flex: 0 0 50%;

					max-width: 50%;
				}
			}

			&--66 {
				@include above($tablet) {
					flex: 0 0 66.666%;

					max-width: 66.666%;
				}
			}
		}

		.form__label {
			@include text-base-medium();
			@include indent-level(margin-bottom, red);
		}
	}

	&__row-controls {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -.8rem;
	}

	&__row-controls--v-center {
		align-items: center;
	}

	&__radio-group {
		display: flex;
		flex-wrap: wrap;

		margin-bottom: -1.2rem;

		@include margin-x(-.6rem);

		.radio {
			margin-bottom: 1.2rem;

			@include padding-x(.6rem);
		}
	}

	&__buttons {
		& > [class*='col-'] {
			margin-right: 4.5rem;

			&:last-of-type {
				margin-right: 0;
			}
		}
	}
}
