.announcing {
	&__link {
		position: relative;

		display: block;

		&::after {
			position: relative;

			display: block;

			width: 100%;
			padding-bottom: 50%;

			content: ' ';

			@include above($tablet) {
				display: none;
			}
		}
	}

	&__inner {
		position: absolute;
		top: 0;
		left: 0;

		display: flex;

		width: 100%;
		height: 100%;
		min-height: 16.7rem;
		padding: 3rem 1.6rem 2rem;

		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;

		@include above($tablet) {
			position: relative;

			width: auto;
			min-height: 31rem;
			padding-left: 2rem;
			padding-right: 2rem;
		}

		@include above($mid-desktop) {
			min-height: 42rem;
			padding-left: 4rem;
			padding-right: 4rem;
			padding-bottom: 6rem;
		}
	}

	&__info {
		margin-top: auto;
	}

	&__title {
		line-height: 1.3;

		@include indent-level(margin-bottom, orange);

		@include above($tablet) {
			font-size: 2.5rem;
		}

		@include above($mid-desktop) {
			font-size: 3rem;
		}
	}

	&__desc {
		font-size: 1.4rem;

		width: 60%;
	}

	&--slide {
		@include above($tablet) {
			width: 62rem;
			min-width: 62rem;
		}

		@include above($mid-desktop) {
			width: 84rem;
			min-width: 84rem;
		}
	}
}
