.old-site-block {
	color: color(dark-main);

	strong {
		font-weight: $bold-weight;
	}

	p {
		@include text-base-large;

		&:not(:first-child) {
			@include indent-level(margin-top, yellow);
		}

		&:not(:last-child) {
			@include indent-level(margin-bottom, yellow);
		}

		strong {
			font-weight: $bold-weight;
		}
	}

	h1 {
		margin-bottom: 0;

		@include title-1;
	}

	h2 {
		@include title-2;
		@include indent-level(margin-top, purple);
		@include indent-level(margin-bottom, blue);

		&:first-child {
			margin-top: 0;
		}
	}

	h3 {
		@include title-3;
		@include indent-level(margin-top, blue);
		@include indent-level(margin-bottom, green);

		&:first-child {
			margin-top: 0;
		}
	}

	h4 {
		font-weight: $bold-weight;

		margin-top: 3rem;
		margin-bottom: 2rem;

		@include title-4;

		&:first-child {
			margin-top: 0;
		}
	}

	ul {
		&:not(:last-child) {
			@include indent-level(margin-bottom, green);
		}

	}

	ul li {
		@include ul-li;
		padding-left: 0;

		&:before {
			position: static;
			display: inline-block;
			margin-right: 1rem;
			margin-left: .4rem;
			transform: translateY(-.2rem);
		}
	}

	ol {
		padding-left: 2.6rem;

		list-style: decimal;

		counter-reset: item;

		&:not(:last-child) {
			@include indent-level(margin-bottom, green);
		}
	}

	ol li {
		padding-left: .6rem;
		&:not(:last-child) {
			@include indent-level(margin-bottom, yellow);
		}
	}

	.conpic {
		margin-top: 3rem;
		margin-bottom: 3rem;

		img {
			width: auto;
			max-width: 100%;
		}

		p {
			margin-top: 1rem;

			color: color(dark-70);
		}
	}

	img {
		width: auto;
		max-width: 100%;

		&[align^="left"] {
			margin-right: 2rem;
			margin-bottom: 1.2rem;
		}
		&[align^="right"] {
			margin-left: 2rem;
			margin-bottom: 1.2rem;
		}
	}

	table {
		width: 100%;

		@include above($big-desktop) {
			width: 80%;
			margin: 0 auto;
		}

		caption {
			margin-bottom: 2rem;
		}

		p {
			margin-bottom: 1rem;

			&:last-of-type {
				margin-bottom: 0;
			}
		}
	}

	td,
	th {
		padding: 1.7rem;
	}

	td {
		border-bottom: .1rem solid color(dark-10);
	}

	th {
		border-top: .1rem solid color(dark-10);
	}

	tr {
		&:first-of-type {
			td {
				border-top: .1rem solid color(dark-10);
			}
		}
	}

	.table-responsive {
		overflow: auto;

		width: 100%;
		max-width: 100%;
		margin-bottom: 3rem;

		img {
			max-width: unset;
		}
	}

	blockquote {
		margin-top: 2rem;
		margin-bottom: 2rem;

		.jumbotron {
			@include above($big-desktop) {
				margin-left: 2.6rem;
			}
		}
	}

	a {
		color: $color-base;
		transition: color $transition;
		&:hover {
			@media (hover: hover) {
				color: $color-base-hover;
			}
		}
	}

	//figure, figcaption
	figure {


		@include indent-level(margin-bottom, green);

		@include above($tablet) {
			width: 40rem;
			float: left;
			padding-right: 6rem;
		}

		img {
			display: block;
			max-width: 100%;
			@include indent-level(margin-bottom, orange);
		}
	}
	figcaption {
		font-size: 1.2rem;
		line-height: 1.4;
		color: color('dark-70');
	}

}

