.tingle-modal {
	overflow-y: auto !important;

	padding: 2rem 0 0;

	background: rgba(color('dark-main'), .5);

	@include above($phone-landscape) {
		padding: 2rem;
	}

	&::before {
		display: none;
	}

	&__close {
		display: none;

		@media (max-width: 540px) {
			display: none;
		}
	}

	@media (max-width: 540px) {
		display: flex;
		overflow-y: hidden !important;
		padding: 2rem 0 0;
	}
}

.tingle-modal-box {
	width: 100%;
	margin-bottom: 0;
	pointer-events: none;

	background: transparent;

	@include above($phone-landscape) {
		margin-bottom: auto;
	}

	&__content {
		padding: 0;
	}

	.tingle-modal--visible & {
		animation: scale $transition forwards;

		@keyframes scale {
			0% {
				opacity: 0;
			}

			100% {
				opacity: 1;
			}
		}

		@include below($phone-landscape - 1) {
			animation: from-bottom-modal .5s ease forwards;

			@keyframes from-bottom-modal {
				0% {
					transform: translateY(100%);
				}

				100% {
					transform: translateY(0%);
				}
			}
		}
	}

	@media (max-width: 540px) {
		width: 100%;
		max-height: 100%;
		overflow-y: scroll;
		pointer-events: auto;

	}
}

.modal {
	position: relative;

	overflow: hidden;

	width: 100%;

	height: auto !important;
	margin: 0 auto;

	padding: 0;
	pointer-events: all;


	background-color: #ffffff;

	box-shadow: 0 0 1.6rem rgba(46, 97, 172, .24);

	@include above($phone-landscape) {
		max-width: 44rem;
	}

	&__row {
		display: flex;
		flex-wrap: wrap;
	}

	&__row--reverse {
		@include above($tablet) {
			flex-direction: row-reverse;
			flex-wrap: nowrap;
		}
	}

	&__col {
		width: 100%;
	}

	&__col--width--60 {
		@include above($tablet) {
			max-width: 60%;
		}
	}

	&__col--width--40 {
		@include above($tablet) {
			max-width: 40%;
		}
	}

	&__header {
		padding: 1.5rem;

		@include above($phone-landscape) {
			padding: 3rem 4rem;
		}
	}

	&__header--color--blue {
		background-color: color(blue-bg);
	}

	&__body {
		padding: 1.5rem;

		background-color: color(white-main);

		@include above($phone-landscape) {
			padding: 3rem 4rem;
		}
	}

	&__body--color--blue {
		background-color: color(blue-bg);
	}

	&__body--pt-0 {
		padding-top: 0;

		@include above($phone-landscape) {
			padding-top: 0;
		}
	}

	&__body--p-0 {
		padding: 0;

		@include above($phone-landscape) {
			padding: 0;
		}
	}

	&__close {
		position: absolute;
		top: 1.5rem;
		right: 1.5rem;
		z-index: 10;
		cursor: pointer;

		@include above($tablet) {
			top: 2.5rem;
			right: 2.5rem;
		}
	}

	&__title {
		padding-right: 2.5rem;

		@include above($tablet) {
			padding-right: 0;
		}
	}

	&__iframe {
		position: relative;

		overflow: hidden;

		width: 100%;
		height: 0;
		padding-top: 56.25%;

		iframe {
			position: absolute;
			top: 0;
			left: 0;

			display: block;

			width: 100%;
			height: 100%;

			background: #000000;
			box-shadow: 0 0 8px rgba(0, 0, 0, .6);
		}
	}

	&--size--medium {
		@include above($phone-landscape) {
			max-width: 74rem;
		}
	}

	&--size--big {
		@include above($phone-landscape) {
			max-width: 100rem;
		}
	}

	&--size--xl {
		@include above($phone-landscape) {
			max-width: 90vw;
		}
	}

	&--size--auto {
		@include above($phone-landscape) {
			max-width: 100%;
		}
	}

	&--color--blue {
		background-color: color(blue-bg);
	}
}
